/* 7.1 Setting */
.page-template-template-homepage-php .wpb_row .vc_col-sm-12 .wpb_row,.page-template-template-blog-php .wpb_row .vc_col-sm-12 .wpb_row {
  margin-left:auto;
  margin-right:auto;
}
.boxed .section .section_inner, .grid_section .section_inner {
  margin: 0 auto;
  position: relative;
  width: 1170px;
  z-index: 20;
}
.wpb_row, .wpb_content_element, ul.wpb_thumbnails-fluid &>li, .last_toggle_el_margin, .wpb_button {
  margin-bottom: 0!important;
}
.tz_remove_padding {
  margin-left:-15px;
  margin-right:-15px;
}
.clr {
  clear: both;
}
.parallax {
  background-attachment: fixed !important;
  background-position: 50% 0;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.overlay_parallax {
  height: 100%;
  left: 0;
  position: absolute !important;
  top: 0;
  width: 100%;
}
.bk-responsive-slide {
  position: absolute;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  z-index: 1;
}
.tzGmap-overlay {
  position: absolute;
  left: 0;
  right: 0;
  background: rgba(0,0,0,0.7);
  width: 100%;
  height: 100%;
  z-index: 1;
}
.wpb_gmaps_widget {
  .wpb_wrapper {
    padding: 0 !important;
  }
}
.rev_slider {
  z-index: 0;
}
.tzHyphenbottom:after {
  position:relative;
  width: auto;
  left: 50%;
  bottom: 0;
}
body {
  .vc_row {
    margin:0;
  }
  .vc_col_noPadding {
    padding:0;
  }
}

.vc_row-fluid {
  & > .container {
    padding:0;
  }
  >.no_container{
    >.vc_col-sm-12{
      padding:0;
    }
  }
}
.wpb_content_element {
  p {
    font-size: 30px;
    color:@color-text-h2;
    letter-spacing: 1px;
    span {
      font-weight: 700;
    }
  }
  blockquote {
    border-left:10px solid #f3f3f3;
    padding:0 0 0 48px;
    p {
      font-size:22px;
      line-height:40px;
      font-weight: 300;
    }
  }
}
.wpb_content_element.quote_dark {
  blockquote {
    border-color:rgba(243,243,243,0.2);
  }
}
.tzSpace_default {
  padding: 50px 0 100px 0;
  position: relative;
}
.tzRowLine_left {
  width:7px;
  height:30px;
  background:#e45914;
  position:absolute;
  top:-15px;
  left:50%;
  margin-left:-10.5px;
}
.tp-caption {
  a:hover {
    text-decoration: none;
  }
}
.tzRowLine_left:after {
  content:'';
  width:100%;
  height:50%;
  background:rgba(0, 0, 0, 0.2);
  position:absolute;
  bottom:0;
  left: 0;
}
.tzRowLine_right {
  width:7px;
  height:30px;
  background:#e45914;
  position:absolute;
  top:-15px;
  right:50%;
  margin-right:-10.5px;
}
.tzRowLine_right:after {
  content:'';
  width:100%;
  height:50%;
  background:rgba(0, 0, 0, 0.2);
  position:absolute;
  bottom:0;
  left: 0;
}
.tzRowNumber {
  width:64px;
  height:64px;
  background:#e45914;
  position: absolute;
  top:-32px;
  left:50%;
  margin-left:-32px;
  transform: rotate(45deg);
  -webkit-transform:rotate(45deg);
  -moz-transform:rotate(45deg);
  -ms-transform:rotate(45deg);
  -o-transform:rotate(45deg);
  span {
    display: block;
    font-family: 'Roboto Slab', serif;
    color:#fff;
    font-weight:400;
    font-size:40px;
    text-align: center;
    transform: rotate(-45deg);
    -webkit-transform:rotate(-45deg);
    -moz-transform:rotate(-45deg);
    -ms-transform:rotate(-45deg);
    -o-transform:rotate(-45deg);
  }
}
.tzRow_ArrowDown {
  position: absolute;
  top:0;
  left:50%;
  margin-left:-25px;
  width:0;
  height:0;
  border-left:25px solid transparent;
  border-right:25px solid transparent;
  border-top:25px solid #ffffff;
}
.tzRow_Icon {
  width:100%;
  height:25px;
  position:absolute;
  top:0;
  left:0;
  text-align: center;
  i {
    color:@color-theme;
    font-size:15px;
  }
}
.tzRow_ArrowType4 {
  border-left: 60px solid rgba(0, 0, 0, 0);
  border-right: 60px solid rgba(0, 0, 0, 0);
  border-top: 30px solid @color-theme;
  left: 50%;
  margin-left: -62px;
  position: absolute;
  top: 100%;
  z-index: 9999;
}
