/**
 * 3.0 Footer
 * -----------------------------------------------------------------------------
 */
.tz-footer {
  .tz-footerTop {
    position: relative;
    background: none repeat scroll 0 0 #161616;
    .container {
      padding-top:60px;
      padding-bottom:13px;
    }
    .footerattr {
      aside.widget {
        margin-bottom: 60px;
        h3.module-title {
          margin:0 0 30px;
          padding:0;
          border-bottom: 1px solid #373737;
          position: relative;
          display: block;
          span {
            font-family: @font-family-h3;
            font-size: 14px;
            line-height: 24px;
            text-transform: uppercase;
            color:#fff;
            padding:0 0 5px;
            display: inline-block;
            a {
              color:#888888;
              text-decoration: none;
            }
          }
        }
        .module-title {
          &:after {
            width: 65px;
            height: 100%;
            content: "";
            border-top: 1px solid @color-theme;
            position: absolute;
            top:100%;
            left: 0;
            transition: all 0.3s linear 0s;
            -moz-transition: all 0.3s linear 0s;
            -webkit-transition: all 0.3s linear 0s;
            -ms-transition: all 0.3s linear 0s;
            -o-transition: all 0.3s linear 0s;
          }
          &:before {
            background: transparent;
          }
        }
        ul {
          margin:0;
          padding:0;
          li {
            font-weight: 400;
            font-size: 14px;
            line-height: 24px;
            color:#666666;
            list-style: none;
            padding:9px 0 8px;
            border-bottom: 1px solid #000000;
            box-shadow: 0 1px 0 0 #282828;
            -webkit-box-shadow: 0 1px 0 0 #282828;
            -moz-box-shadow: 0 1px 0 0 #282828;
            -ms-box-shadow: 0 1px 0 0 #282828;
            -o-box-shadow: 0 1px 0 0 #282828;
            a {
              color:#666666;
              list-style: none;
            }
          }
          li:hover {
            color:#999999;
            a {
              color:#999999;
              text-decoration: none;
            }
          }
        }
        &.tz_maniva_view_post ul li:after {
          background: transparent;
        }
        select {
          -moz-appearance:none;
          background: url("images/bg_down.png") no-repeat scroll right 20px top 19px rgba(0, 0, 0, 0);
          border:1px solid #2c2c2c;
          font-family: 'Raleway', serif;
          font-weight: 400;
          font-size: 15px;
          line-height: 24px;
          color:#797979;
          width:100%;
          padding:10px 15px;
        }
        .tz-flickr {
          ul.pretty_flickr {
            li {
              box-shadow: none;
              border: none;
              padding: 0;
              margin-right: 7px;
              a {
                img {
                  border:1px solid #333333;
                  padding:4px;
                  width: 58px;
                  transition: all 0.3s ease-in-out 0s;
                  -webkit-transition: all 0.3s ease-in-out 0s;
                  -moz-transition: all 0.3s ease-in-out 0s;
                  -ms-transition: all 0.3s ease-in-out 0s;
                  -o-transition: all 0.3s ease-in-out 0s;
                }
                &:hover img {
                  border-color: @color-theme;
                }
              }
            }
          }
          &:after {
            display: table;
            clear: both;
            content: "";
          }
        }
      }
      aside.widget:hover {
        .module-title:after {
          width: 100%;
        }
      }
      img {
        max-width: 100%;
        height: auto;
      }
      aside.widget_calendar {
        #calendar_wrap {
          table {
            width: 100%;
            caption {
              color: #888888;
              font-family: "Roboto Slab",serif;
              font-size: 14px;
              font-weight: 400;
              line-height: 24px;
              padding: 0 0 5px;
            }
            thead {
              border:1px solid #888888;
              tr {
                th {
                  text-align: center;
                  color: #666666;
                  font-family: "Roboto Slab",serif;
                  font-size: 13px;
                  font-weight: 400;
                  padding:9px;
                }
              }
            }
            tbody {
              border:1px solid #888888;
              border-top: none;
              tr {
                td {
                  text-align: center;
                  color: #666666;
                  font-family: "Roboto Slab",serif;
                  font-size: 13px;
                  font-weight: 400;
                  padding:9px;
                }
                td#today {
                  color:@color-theme;
                }
              }
            }
            tfoot {
              tr {
                td {
                  a {
                    color: #666666;
                    font-family: "Roboto Slab",serif;
                    font-size: 13px;
                    font-weight: 400;
                    padding:6px;
                    display: inline-block;
                  }
                }
              }
            }
          }
        }
      }
      aside.widget_tag_cloud {
        .tagcloud:after {
          content: '';
          clear: both;
          display: block;
        }
        .tagcloud {
          a {
            display: inline-block;
            float: left;
            margin: 0 5px 5px 0;
            padding:3px 10px;
            font-size: 12px!important;
            text-transform: uppercase;
            color:#666666;
            border:1px solid #666666;
            background: transparent;
            transition: all 0.3s ease-in-out 0s;
            -webkit-transition: all 0.3s ease-in-out 0s;
            -moz-transition: all 0.3s ease-in-out 0s;
            -ms-transition: all 0.3s ease-in-out 0s;
            -o-transition: all 0.3s ease-in-out 0s;
          }
          a:hover {
            text-decoration: none;
            color:@color-theme;
            border:1px solid @color-theme;
          }
        }
      }
      aside.widget_search {
        form {
          position: relative;
          label {
            display: none;
          }
          input.Tzsearchform {
            width: 100%;
            background: none;
            border:1px solid #666666;
            font-family: 'Raleway',sans-serif;
            font-size: 14px;
            font-weight: 400;
            padding: 9px 50px 9px 15px;
            color: #444444;
          }
          input.searchsubmit {
            background-color: @color-theme;
            background-image: url(images/search.png);
            background-position: center;
            background-repeat: no-repeat;
            border: none;
            font-size: 0;
            width:40px;
            height:40px;
            position: absolute;
            top:0;
            right:0;
          }
        }
      }
      aside.widget_flickr {
        .flickr-badge-wrapper {
          .flickr_badge_image {
            width: auto;
            height: auto;
            margin: 0;
            padding: 0;
            a {
              img {
                border:1px solid #333333;
                padding:4px;
                margin-right: 7px;
                margin-bottom: 7px;
                width: 58px;
                transition: all 0.3s ease-in-out 0s;
                -webkit-transition: all 0.3s ease-in-out 0s;
                -moz-transition: all 0.3s ease-in-out 0s;
                -ms-transition: all 0.3s ease-in-out 0s;
                -o-transition: all 0.3s ease-in-out 0s;
              }
              &:hover img {
                border:1px solid @color-theme;
              }
            }
          }
        }
      }
      aside.widget_sp_image {
        .widget_sp_image-description {
          margin-top: 15px;
          p {
            color: #666666;
            font-family: "Raleway",serif;
            font-size: 14px;
            font-weight: 400;
            line-height: 24px;
          }
        }
      }
      aside.tzsocial {
        a {
          display: inline-block;
          width: 40px;
          height: 40px;
          margin: 0 10px 10px;
          padding: 10px 0;
          text-align: center;
          border:1px solid #444444;
          transform: rotate(45deg);
          -webkit-transform: rotate(45deg);
          -moz-transform: rotate(45deg);
          -ms-transform: rotate(45deg);
          -o-transform: rotate(45deg);
          i {
            font-size: 21px;
            color: #666666;
            transform: rotate(-45deg);
            -webkit-transform: rotate(-45deg);
            -moz-transform: rotate(-45deg);
            -ms-transform: rotate(-45deg);
            -o-transform: rotate(-45deg);
          }
        }
      }
      aside.widget_contact_info {
        ul.tzcontact-info {
          li {
            border: none;
            box-shadow: none;
            position: relative;
            padding: 0 0 5px 46px;
            i {
              font-size: 21px;
              color: #444444;
              position: absolute;
              top: 3px;
              left: 0;
            }
            span {
              display: block;
              color: #555555;
              font-size: 14px;
              font-family: @font-family-p;
              line-height: 26px;
              a {
                color: #555555;
              }
              a:hover {
                text-decoration: none;
                color: #555555;
              }
              a:focus {
                text-decoration: none;
                color: #555555;
              }
            }
            &:hover a {
              color: #555555;
            }
          }
        }
      }
    }
  }
  .tz-footerBottom {
    position: relative;
    background: none repeat scroll 0 0 #2a2a2a;
    .tz-backtotop {
      width:50px;
      height:50px;
      left: 50%;
      margin-left: -25px;
      margin-top: -25px;
      position: absolute;
      top: 0;
      z-index: 999;
      img {
        width: 100%;
        height: auto;
      }
    }
    .tz-backtotop:hover {
      cursor: pointer;
    }
    .tzcopyright {
      p {
        font-family: 'Roboto Slab', serif;
        font-weight: 400;
        font-size: 12px;
        text-transform: uppercase;
        color:#888888;
        margin:0;
        padding:24px 0 30px;
        a {
          color:#888888;
          text-decoration: none;
        }
      }
    }
    .tzbottom_menu {
      nav.tzfootermenu {
        ul {
          margin:0;
          padding:0;
          li {
            list-style: none;
            float: left;
            margin: 30px 0;
            a {
              font-weight: 400;
              font-size: 10px;
              line-height: 7px;
              text-transform: uppercase;
              color:#888888;
              padding:0 7px;
              display: block;
              border-right: 1px solid #000000;
              box-shadow: 1px 0 0 0 #555555;
              -moz-box-shadow: 1px 0 0 0 #555555;
              -ms-box-shadow: 1px 0 0 0 #555555;
              -webkit-box-shadow: 1px 0 0 0 #555555;
              -o-box-shadow: 1px 0 0 0 #555555;
            }
            a:hover {
              text-decoration: none;
              color:@color-theme;
            }
          }
          li:last-child {
            a {
              box-shadow: none;
              border:none;
            }
          }
        }
      }
    }
  }
}
