body{
  margin: 0;
  padding: 0;
  color: @color-text-body;
  font-size: 14px;
  line-height: 1.4em;
  font-family: @font-family-p;
  font-weight: 400;
  &#bd {
    position: relative;
    overflow-x: hidden;
  }
}
p {
  margin: 0;
}
h1,
h2,
h3 {
  font-family: @font-family-h3;
  color: @color-text-h2;
  margin: 0;
  padding: 0;
  line-height: 1.5em;
}

h4,
h5,
h6 {
  font-family: @font-family-p;
  color: @color-text-h2;
  margin: 0;
  padding: 0;
  line-height: 1.4em;
}

.ds-table {
  display: table;
  height: 100%;
  width: 100%;
}
.ds-table-cell {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
.searchform {
  i.tz-form-close,
  i.tz-icon-form-search {
    display: none;
  }
}
.ds-table-cell-style {
  display: inline-block;
  vertical-align: middle;
}
.bypostauthor,
.sticky{
  color: @color-theme;
}
div a:focus{
  outline:none;
}
.entry-content {
  img {
    margin: 0 0 1.5em 0;
  }
}
.alignleft, img.alignleft {
  margin: 0.4211em 1.6842em 1.6842em 0;
  display: inline;
  float: left;
}
.alignright, img.alignright {
  margin: 0.4211em 1.6842em 1.6842em 0;
  display: inline;
  float: right;
}
.aligncenter, img.aligncenter {
  margin-right: auto;
  margin-left: auto;
  display: block;
  clear: both;
}
embed, iframe, object, video {
  max-width: 100%;
}
.wp-caption {
  margin-bottom: 1.5em;
  text-align: center;
  padding-top: 5px;
  max-width: 100%!important;
  img {
    border: 0 none;
    padding: 0;
    margin: 0;
    max-width: 100%;
    height: auto;
  }
  p.wp-caption-text {
    line-height: 1.5;
    font-size: 10px;
    margin: 0;
  }
}
.wp-smiley {
  margin: 0 !important;
  max-height: 1em;
}
blockquote.left {
  margin-right: 20px;
  text-align: right;
  margin-left: 0;
  width: 33%;
  float: left;
}
blockquote.right {
  margin-left: 20px;
  text-align: left;
  margin-right: 0;
  width: 33%;
  float: right;
}

@font-face {
  font-family: 'latoblack';
  src: url('fonts/lato-black/Lato-Black-webfont.eot');
  src: url('fonts/lato-black/Lato-Black-webfont.eot?#iefix') format('embedded-opentype'),
  url('fonts/lato-black/Lato-Black-webfont.woff2') format('woff2'),
  url('fonts/lato-black/Lato-Black-webfont.woff') format('woff'),
  url('fonts/lato-black/Lato-Black-webfont.ttf') format('truetype'),
  url('fonts/lato-black/Lato-Black-webfont.svg#latoblack') format('svg');
  font-weight: normal;
  font-style: normal;
}

/* Assistive text */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  position: absolute !important;
  height: 1px;
  width: 1px;
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-weight: bold;
  height: auto;
  line-height: normal;
  padding: 15px 23px 14px;
  position: absolute;
  left: 5px;
  top: 5px;
  text-decoration: none;
  width: auto;
  z-index: 100000; /* Above WP toolbar */
}

input:focus {
  outline: -webkit-focus-ring-color auto 0;
}
#easy-opener-section {
  display: none;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 10001;
}
.easy-opener-bg {
  background: #0d0d0d none repeat scroll 0 0;
  height: 100%;
  left: 0;
  opacity: 0.97;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: -1;
}
.easy-opener-content {
  left: 50%;
  margin-left: -500px;
  opacity: 0;
  position: absolute;
  text-align: center;
  top: 50%;
  width: 1000px;
}
.easy-opener-content img {
  max-height: 100%;
  max-width: 100%;
}
.easy-opener-close {
  background: rgba(0, 0, 0, 0) url("images/sprites.png") no-repeat scroll -280px 0;
  display: block;
  height: 30px;
  left: 50%;
  margin-left: -15px;
  position: absolute;
  text-indent: -9999px;
  top: -60px;
  transition: opacity 0.1s ease-in-out 0s;
  width: 30px;
}
.easy-opener-next {
  background: rgba(0, 0, 0, 0) url("images/sprites.png") no-repeat scroll -160px 0;
  display: block;
  height: 30px;
  margin-right: -50px;
  position: absolute;
  right: 50%;
  text-indent: -9999px;
  top: -60px;
  transition: opacity 0.1s ease-in-out 0s;
  width: 30px;
}
.easy-opener-prev {
  background: rgba(0, 0, 0, 0) url("images/sprites.png") no-repeat scroll -190px 0;
  display: block;
  height: 30px;
  left: 50%;
  margin-left: -50px;
  position: absolute;
  text-indent: -9999px;
  top: -60px;
  transition: opacity 0.1s ease-in-out 0s;
  width: 30px;
}
.easy-opener-close:hover,
.easy-opener-next:hover,
.easy-opener-prev:hover {
  opacity: 0.7;
}
.fluid-width-video-wrapper iframe,
.fluid-width-video-wrapper object,
.fluid-width-video-wrapper embed {
  border: medium none;
}
.isotope-item {
  z-index: 2;
}
.isotope-hidden.isotope-item {
  pointer-events: none;
  z-index: 1;
}
.isotope,.isotope .isotope-item {
  -webkit-transition-duration: 0.8s;
  -moz-transition-duration: 0.8s;
  -ms-transition-duration: 0.8s;
  -o-transition-duration: 0.8s;
  transition-duration: 0.8s;
}
.isotope {
  -webkit-transition-property: height, width;
  -moz-transition-property: height, width;
  -ms-transition-property: height, width;
  -o-transition-property: height, width;
  transition-property: height, width;
  .isotope-item {
    -webkit-transition-property: -webkit-transform, opacity;
    -moz-transition-property:    -moz-transform, opacity;
    -ms-transition-property:     -ms-transform, opacity;
    -o-transition-property:      -o-transform, opacity;
    transition-property:         transform, opacity;
  }
}
.isotope.no-transition,.isotope.no-transition .isotope-item,.isotope .isotope-item.no-transition {
  -webkit-transition-duration: 0s;
  -moz-transition-duration: 0s;
  -ms-transition-duration: 0s;
  -o-transition-duration: 0s;
  transition-duration: 0s;
}
.isotope.infinite-scrolling {
  .transition(none);
}
.owl-carousel {
  display: none;
  width: 100%;
  -ms-touch-action: pan-y;
  .owl-wrapper:after {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0;
  }
  .owl-wrapper {
    display: none;
    position: relative;
    -webkit-transform: translate3d(0px, 0px, 0px);
  }
  .owl-wrapper-outer {
    overflow: hidden;
    position: relative;
    width: 100%;
  }
  .owl-wrapper-outer.autoHeight {
    .transition(height 500ms ease-in-out);
  }
  .owl-item {
    float: left;
  }
}
.owl-controls .owl-page,.owl-controls .owl-buttons div {
  cursor: pointer;
}
.owl-controls {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.owl-carousel  .owl-wrapper,.owl-carousel  .owl-item {
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility:    hidden;
  -ms-backface-visibility:     hidden;
  .transform(translate3d(0,0,0));
}
.owl-theme {
  .owl-controls {
    margin-top: 10px;
    text-align: center;
    .owl-buttons {
      div {
        color: #FFF;
        display: inline-block;
        zoom: 1;
        *display: inline;
        margin: 5px;
        padding: 3px 10px;
        font-size: 12px;
        .border-radius(30px);
        background: #869791;
        filter: Alpha(Opacity=50);
        opacity: 0.5;
      }
    }
    .owl-page {
      display: inline-block;
      zoom: 1;
      *display: inline;
      span {
        display: block;
        width: 12px;
        height: 12px;
        margin: 5px 7px;
        filter: Alpha(Opacity=50);
        opacity: 0.5;
        .border-radius(20px);
        background: #869791;
      }
      span.owl-numbers {
        height: auto;
        width: auto;
        color: #FFF;
        padding: 2px 10px;
        font-size: 12px;
        .border-radius(30px);
      }
    }
  }
  .owl-controls.clickable {
    .owl-buttons {
      div:hover {
        filter: Alpha(Opacity=100);
        opacity: 1;
        text-decoration: none;
      }
    }
  }
}
.owl-theme .owl-controls .owl-page.active span,.owl-theme .owl-controls.clickable .owl-page:hover span {
  filter: Alpha(Opacity=100);
  opacity: 1;
}
.owl-item.loading {
  min-height: 150px;
}
@font-face {
  font-family: 'flexslider-icon';
  src:url('fonts/flexslider-icon.eot');
  src:url('fonts/flexslider-icon.eot?#iefix') format('embedded-opentype'),
  url('fonts/flexslider-icon.woff') format('woff'),
  url('fonts/flexslider-icon.ttf') format('truetype'),
  url('fonts/flexslider-icon.svg#flexslider-icon') format('svg');
  font-weight: normal;
  font-style: normal;
}
.flex-container a:active,.flexslider a:active,.flex-container a:focus,.flexslider a:focus {
  outline: none;
}
.slides,.flex-control-nav,.flex-direction-nav {
  margin: 0;
  padding: 0;
  list-style: none;
}
.flexslider {
  padding: 0;
  margin: 0 0 60px;
  background: #fff;
  border: 4px solid #fff;
  position: relative;
  .border-radius(4px);
  .box-shadow(0 1px 4px rgba(0,0,0,.2));
  zoom: 1;
  .slides {
    zoom: 1;
    & > li {
      display: none;
      -webkit-backface-visibility: hidden;
    }
    img {
      width: 100%;
      display: block;
    }
  }
}
.flex-pauseplay {
  span {
    text-transform: capitalize;
  }
  a {
    display: block;
    width: 20px;
    height: 20px;
    position: absolute;
    bottom: 5px;
    left: 10px;
    opacity: 0.8;
    z-index: 10;
    overflow: hidden;
    cursor: pointer;
    color: #000;
  }
  a:before {
    font-family: "flexslider-icon";
    font-size: 20px;
    display: inline-block;
    content: '\f004';
  }
  a:hover {
    opacity: 1;
  }
  a.flex-play:before {
    content: '\f003';
  }
}
.slides:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}
html[xmlns] {
  .slides {
    display: block;
  }
}
* {
  html {
    .slides {
      height: 1%;
    }
  }
}
/* If you are not using another script, such as Modernizr, make sure you* include js that eliminates this class on page load */.no-js .slides &>li:first-child {
                                                                                                                                 display: block;
                                                                                                                               }
.flex-viewport {
  max-height: 2000px;
  .transition(all 1s ease);
}
.loading {
  .flex-viewport {
    max-height: 300px;
  }
}
.carousel {
  li {
    margin-right: 5px;
  }
}
.flex-direction-nav {
  *height: 0;
  a {
    text-decoration:none;
    display: block;
    width: 40px;
    height: 40px;
    margin: -20px 0 0;
    position: absolute;
    top: 50%;
    z-index: 10;
    overflow: hidden;
    opacity: 0;
    cursor: pointer;
    color: rgba(0,0,0,0.8);
    text-shadow: 1px 1px 0 rgba(255,255,255,0.3);
    .transition(all .3s ease);
  }
  .flex-prev {
    left: -50px;
  }
  .flex-next {
    right: -50px;
    text-align: right;
  }
  .flex-disabled {
    opacity: 0!important;
    filter:alpha(opacity=0);
    cursor: default;
  }
  a:before {
    font-family: 'FontAwesome';
    font-size: 40px;
    line-height:1;
    display: inline-block;
    content: '\f104';
  }
  a.flex-next:before {
    content: '\f105';
  }
}
.flexslider:hover {
  .flex-prev {
    opacity: 0.7;
    left: 10px;
  }
  .flex-next {
    opacity: 0.7;
    right: 10px;
  }
}
.flexslider:hover .flex-next:hover, .flexslider:hover .flex-prev:hover {
  opacity: 1;
}
.flex-control-nav {
  width: 100%;
  position: absolute;
  bottom: -40px;
  text-align: center;
  li {
    margin: 0 6px;
    display: inline-block;
    zoom: 1;
    *display: inline;
  }
}
.flex-control-paging {
  li {
    a {
      width: 11px;
      height: 11px;
      display: block;
      background: #666;
      background: rgba(0,0,0,0.5);
      cursor: pointer;
      text-indent: -9999px;
      .border-radius(20px);
      .box-shadow(inset 0 0 3px rgba(0,0,0,0.3));
    }
    a:hover {
      background: #333;
      background: rgba(0,0,0,0.7);
    }
    a.flex-active {
      background: #000;
      background: rgba(0,0,0,0.9);
      cursor: default;
    }
  }
}
.flex-control-thumbs {
  margin: 5px 0 0;
  position: static;
  overflow: hidden;
  li {
    width: 25%;
    float: left;
    margin: 0;
  }
  img {
    width: 100%;
    display: block;
    opacity: .7;
    cursor: pointer;
  }
  img:hover {
    opacity: 1;
  }
  .flex-active {
    opacity: 1;
    cursor: default;
  }
}
.eg-adams-container {
