/* 4.2 Blog full width */
.tz-blogDefault {
  padding:100px 0 80px;
  &.tz_blogDefault_breadcrumb {
    padding-top: 40px;
  }
  .tz-blogItem:after {
    content: '';
    display: block;
    clear: both;
  }
  .tz-blogItem {
    .tz-blogContent {
      margin-bottom: 40px;
      .tz-blogBox {
        border-bottom: 2px solid @color-theme;
        padding: 30px 30px 35px;
        background: #f8f8f9;
        .tz-BlogImage {
          position: relative;
          margin-bottom: 32px;
          .border-radius (5px);
          overflow: hidden;
          img {
            display: block;
            max-width: 100%;
            height: auto;
            margin: 0 auto;
          }
          .tz-ImageOverlay {
            position: absolute;
            top:0;
            right: 0;
            background-color: rgba(0, 0, 0, 0.4);
            .transform(scale(0.7));
            .transition(all 0.3s ease-in-out 0s);
            opacity: 0;
            width: 100%;
            height: 100%;
          }
          a {
            position: absolute;
            top:30%;
            width:40px;
            height:40px;
            background-color:#fff;
            margin-top: -20px;
            text-align: center;
            border-radius: 50%;
            -webkit-border-radius: 50%;
            -ms-border-radius: 50%;
            -moz-border-radius: 50%;
            -o-border-radius: 50%;
            visibility: hidden;
            opacity: 0;
            i {
              color:#000;
              padding:14px 0;
              &.fa-link {
                transform: rotate(90deg);
                -moz-transform: rotate(90deg);
                -webkit-transform: rotate(90deg);
                -ms-transform: rotate(90deg);
                -o-transform: rotate(90deg);
              }
            }
          }
          a.tz-ViewImage {
            left: 50%;
            margin-left: -44px;
          }
          a.tz-ViewDetail {
            right: 50%;
            margin-right: -44px;
          }
          a:hover {
            i {
              color:#fff;
            }
          }
        }
        .tz-BlogImage:hover {
          .tz-ImageOverlay {
            .transform(scale(1));
            opacity: 1;
          }
          a.tz-ViewImage {
            top: 50%;
            visibility: visible;
            opacity: 1;
            transition: all 0.3s ease-in 0s;
            -moz-transition: all 0.3s ease-in 0s;
            -webkit-transition: all 0.3s ease-in 0s;
            -ms-transition: all 0.3s ease-in 0s;
            -o-transition: all 0.3s ease-in 0s;
          }
          a.tz-ViewDetail {
            top: 50%;
            visibility: visible;
            opacity: 1;
            transition: all 0.3s ease-in 0.1s;
            -moz-transition: all 0.3s ease-in 0.1s;
            -webkit-transition: all 0.3s ease-in 0.1s;
            -ms-transition: all 0.3s ease-in 0.1s;
            -o-transition: all 0.3s ease-in 0.1s;
          }
        }
        .tz-blogSlider {
          position: relative;
          margin-bottom: 35px;
          overflow: hidden;
          .border-radius (5px);
          &:before {
            position: absolute;
            content: '';
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: rgba(0,0,0,0.2);
            z-index: 1;
          }
          ol.flex-control-nav{
            bottom: 10px;
            z-index: 2;
            li {
              margin:0 3px;
              a {
                background: #fff;
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                -ms-border-radius: 50%;
                -o-border-radius: 50%;
                box-shadow: none;
                height: 12px;
                width: 12px;
                &.flex-active{
                  background: @color-theme;
                }
              }
            }
          }
          ul {
            li {
              img {
                width: 100%;
                height: auto;
              }
            }
          }
        }
        iframe {
          border:none;
          border-radius: 0;
        }
        h4.title {
          margin:0 0 8px;
          padding:0;
          font-size: 30px;
          font-weight: 600;
          line-height: 1.2em;
          a {
            color: @color-text-h2;
          }
          a:hover {
            text-decoration: none;
          }
        }
        .tzinfomation {
          display: block;
          margin-bottom: 28px;
        }
        p {
          color: #646569;
          margin-bottom: 12px;
          line-height: 1.7em;
          span {
            color: @color-theme;
          }
        }
        a.tzreadmore {
          color: @color-text-h2;
          display:block;
          font-size: 11px;
          font-weight: 600;
          text-decoration: none;
          text-transform: uppercase;
          text-align: right;
          letter-spacing: 2px;
          i{
            margin-left: 10px;
          }
          span {
            &:hover{
              color:@color-theme;
            }
          }

        }
        &.tz-blogBoxSlider {
          background: @color-text-h2;
          h4.title {
            a {
              color: #ffffff;
            }
          }
          .tzinfomation {
            small,
            span,
            a {
              color: #cccccc;
            }
          }
          p {
            color: #a4a8b5;
          }
          a.tzreadmore {
            color: #ffffff;
          }
        }
        &.tz-blogBoxVideo,
        &.tz-blogBoxSoundcloud {
          padding: 0;
          .tz_blog_box_content {
            padding: 0 30px 38px;
          }
        }
        &.tz-blogBoxQuote {
          border-bottom: none;
          padding: 0;
          background: none;
          .tz-blogQuote {
            position: relative;
            .border-radius(5px);
            overflow: hidden;
            &:after {
              position: absolute;
              background: rgba(36,39,50,0.9);
              content: '';
              width: 100%;
              height: 100%;
              top: 0;
              left: 0;
            }
            .tz-blogQuote_detail {
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              z-index: 1;
              padding: 0 86px;
              i.fa {
                font-size: 40px;
                color: @color-theme;
                margin-bottom: 30px;
                transform: rotateY(180deg);
                -moz-transform: rotateY(180deg);
                -webkit-transform: rotateY(180deg);
                -ms-transform: rotateY(180deg);
                -o-transform: rotateY(180deg);
              }
              p {
                font-size: 26px;
                font-weight: 500;
                color: #ffffff;
                line-height: 1.4em;
                margin: 0;
              }
              span.tz-blogQuote_author {
                color: @color-theme;
                font-family: @font-family-lato;
                display: inline-block;
                margin-top: 25px;
                line-height: 1.5em;
              }
            }
            .tz_blogQuote_information {
              position: absolute;
              bottom: 30px;
              left: 30px;
              z-index: 1;
              span.tzinfomation {
                margin-bottom: 0;
                color: #cccccc;
                small {
                  color: #cccccc;
                }
                a {
                  color: #cccccc;
                }
              }
            }
            .tz_blogQuote_link {
              position: absolute;
              right: 30px;
              bottom: 30px;
              z-index: 1;
              a.tzreadmore {
                color: #ffffff;
              }
            }
          }
        }
      }
    }
    .tz-blogBox {
      .tz_stickyPost {
        height: 159px;
        top: -38px;
        position: absolute;
        right: -30px;
        width: 107px;
        background-image: url(images/bg_sticky.png);
        background-repeat: no-repeat;
        span {
          color: #fff;
          display: block;
          font-family: "Raleway",sans-serif;
          font-size: 18px;
          left: -28px;
          line-height: 30px;
          position: absolute;
          top: 60px;
          transform: rotate(90deg);
          -moz-transform: rotate(90deg);
          -webkit-transform: rotate(90deg);
          -ms-transform: rotate(90deg);
          -o-transform: rotate(90deg);
        }
      }
    }
    .tz-blogAudio {
      .tz-blogDate {
        top: 60px;
      }
    }
  }
  .tz-blogItem:last-child {
    .tz-blogContent {
      border-bottom: 0;
    }
  }
  .wp-pagenavi {
    text-align: right;
    margin-top: 90px;
  }
}
.tz-blogDefault .tz-blogItem .tz-blogContent .tz-blogBox .tz-BlogImage a:hover span,
.tz-blogDefault .tz-blogItem .tz-blogContent .tz-blogBox .tz-BlogImage a:hover {
  background-color: @color-theme;
  transition: none;
}
.tz-blogDefault .tz-blogItem .tz-blogContent .tz-blogBox .tz-blogAudio,
.tz-blogDefault .tz-blogItem .tz-blogContent .tz-blogBox .tz-blogVideo {
  position: relative;
  margin-bottom: 28px;
}
.iframe-full-audio {
  height: 350px;
  width:100%;
}
.iframe-full {
  width:100%;
  height: 474px;
}
.tz-blogDefault .tz-blogItem .tz-blogContent .tz-blogBox .tzinfomation small,
.tz-blogDefault .tz-blogItem .tz-blogContent .tz-blogBox .tzinfomation span,
.tz-blogDefault .tz-blogItem .tz-blogContent .tz-blogBox .tzinfomation a {
  font-family: @font-family-lato;
  font-size: 14px;
  font-style: italic;
  color: #95979d;
  text-decoration: none;
  text-shadow: none;
}
.tz-blogDefault .tz-blogItem .tz-blogContent .tz-blogBox .tzinfomation > small,
.tz-blogDefault .tz-blogItem .tz-blogContent .tz-blogBox .tzinfomation > span,
.tz-blogDefault .tz-blogItem .tz-blogContent .tz-blogBox .tzinfomation > a {
  margin-right: 10px;
}
.tz-blogDefault .tz-blogItem .tz-blogContent .tz-blogBox .tzinfomation small i,
.tz-blogDefault .tz-blogItem .tz-blogContent .tz-blogBox .tzinfomation a i,
.tz-blogDefault .tz-blogItem .tz-blogContent .tz-blogBox .tzinfomation span i {
  margin-right: 10px;
  color:#cccccc;
}
.tz-blogDefault .tz-blogItem .tz-blogContent .tz-blogBox .tzinfomation small.tzinfomation_time {
  font-style: normal;
}
.tz-blogDefault .wp-pagenavi a,
.tz-blogDefault .wp-pagenavi span {
  font-size: 16px;
  color:#000000;
  line-height: 24px;
  text-decoration: none;
  padding:10px 15px;
  border:1px solid #dddddd;
  font-weight: 400;
  font-family: @font-family-lato;
  display: inline-block;
}
.tz-blogDefault .wp-pagenavi span.current,
.tz-blogDefault .wp-pagenavi a:hover,
.tz-blogDefault .wp-pagenavi span:hover {
  background-color:@color-theme;
  border:1px solid @color-theme;
  text-decoration: none;
  color: #ffffff;
}

/* slider blog single */
.tz_meetup_slider_blog_background {
  background-image: url(images/bk-paralax-10.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  padding: 113px 0 85px;
  background-position: center center;
  &:after {
    position: absolute;
    background-color: rgba(36,39,50,0.5);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: '';
  }
}
.tz_meetup_slider_blog_single {
  position: relative;
  z-index: 1;
  text-align: center;
  .tz_partner_blog_single_prev {
    background: transparent;
    border: none;
    position: absolute;
    top: 50%;
    left: -75px;
    font-size: 60px;
    margin-top: -30px;
    color: #aaaaaa;
    cursor: pointer;
    transition: all 0.3s ease 0s;
    -webkit-transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -ms-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    &:hover {
      color: @color-theme;
    }
  }
  .tz_partner_blog_single_next {
    background: transparent;
    border: none;
    position: absolute;
    top: 50%;
    right: -75px;
    font-size: 60px;
    margin-top: -30px;
    color: #aaaaaa;
    cursor: pointer;
    transition: all 0.3s ease 0s;
    -webkit-transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -ms-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    &:hover {
      color: @color-theme;
    }
  }
  ul {
    list-style: none;
    li {
      a {
        display: inline-block;
      }
    }
  }
}