/* 7.16 Event Meetup */
.tz_box_event_meetup {
  h3 {
    font-size: 18px;
    text-transform: uppercase;
    &.tz_event_meetup_subtitle {
      font-weight: 300;
      line-height: 1.1em;
    }
  }
}
.tz_event_meettup_box_content {
  position: relative;
  &:before {
    position: absolute;
    content: '';
    left: 0;
    top: 0;
    height: 50px;
    width: 3px;
    background: -moz-linear-gradient(top,  rgba(255,255,255,1) 0%, rgba(255,255,255,0.65) 35%, rgba(255,255,255,0) 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(255,255,255,1)), color-stop(35%,rgba(255,255,255,0.65)), color-stop(100%,rgba(255,255,255,0)));
    background: -webkit-linear-gradient(top,  rgba(255,255,255,1) 0%,rgba(255,255,255,0.65) 35%,rgba(255,255,255,0) 100%);
    background: -o-linear-gradient(top,  rgba(255,255,255,1) 0%,rgba(255,255,255,0.65) 35%,rgba(255,255,255,0) 100%);
    background: -ms-linear-gradient(top,  rgba(255,255,255,1) 0%,rgba(255,255,255,0.65) 35%,rgba(255,255,255,0) 100%);
    background: linear-gradient(to bottom,  rgba(255,255,255,1) 0%,rgba(255,255,255,0.65) 35%,rgba(255,255,255,0) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00ffffff',GradientType=0 );
    z-index: 1;
  }
  &:after {
    position: absolute;
    content: '';
    left: 0;
    bottom: 0;
    height: 130px;
    width: 3px;
    background: -moz-linear-gradient(bottom,  rgba(255,255,255,1) 30%, rgba(238,238,238,0) 100%);
    background: -webkit-gradient(linear, left bottom, left top, color-stop(30%,rgba(255,255,255,1)), color-stop(100%,rgba(238,238,238,0)));
    background: -webkit-linear-gradient(bottom,  rgba(255,255,255,1) 30%,rgba(238,238,238,0) 100%);
    background: -o-linear-gradient(bottom,  rgba(255,255,255,1) 30%,rgba(238,238,238,0) 100%);
    background: -ms-linear-gradient(bottom,  rgba(255,255,255,1) 30%,rgba(238,238,238,0) 100%);
    background: linear-gradient(to top,  rgba(255,255,255,1) 30%,rgba(238,238,238,0) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00eeeeee',GradientType=0 );
    z-index: 1;
  }
}
.tz_event_meetup_content {
  border-left: 3px solid #eeeeee;
  padding-left: 125px;
  margin-top: 40px;
  position: relative;
  .tz_meetup_box_detail {
    position: relative;
    margin-bottom: 20px;
    display: block;
    .tz_meetup_box_detail_custom {
      display: inline-block;
      padding: 22px 40px 25px 22px;
      background: #f8f8f8;
      .border-radius(10px);
    }
    &:before {
      position: absolute;
      width: 20px;
      height: 20px;
      .border-radius(50%);
      background: @color-theme;
      content: '';
      top: 45px;
      left: -136px;
      border: 5px solid #ffffff;
      z-index: 2;
    }
    &:after {
      border-bottom: 20px solid #f8f8f8;
      border-left: 20px solid transparent;
      border-top: 20px solid transparent;
      content: "";
      left: -20px;
      position: absolute;
      top: 15px;
    }
    span.tz_meetup_start_time {
      color: @color-text-h2;
      left: -110px;
      position: absolute;
      top: 45px;
    }
    p {
      color: @color-theme;
      font-size: 14px;
    }
    h4 {
      color: #595c6a;
      font-size: 15px;
      text-transform: uppercase;
    }
    .tz_event_meetup_item_content {
      h5 {
        font-weight: 200;
        color: #0b0c0f;
      }
      p {
        &:first-child {
          margin-top: 10px;
        }
        color: #898b90;
        line-height: 1.6em;
        margin: 0;
        font-weight: 300;
        font-size: 13px;
        em {
          color: #888a8e;
          font-weight: 500;
          font-size: 12px;
          margin-bottom: 10px;
          display: inline-block;
        }
      }
    }
  }
}