/**
 * 9.0 Responsive
 * -----------------------------------------------------------------------------
 */
@media (max-width: 1600px) {
  .tz-homeType1 {
    .container {
      .tzHeaderContainer {
        border-right:none;
      }
    }
    button.tz-search {
      margin: 45px 0 0 15px;
    }
  }
  .tz-headerHome.tz-homeType1 {
    nav {
      ul.tz-nav {
        & > li {
          & > a {
            font-size: 11px;
          }
        }
      }
    }
    a.tz_logo {
      padding: 42px 40px 40px 0;
    }
  }
  span.tzNumberSection {
    left:0;
  }
  .tzOurTeam1_Container {
    .tzOurTeamBox {
      .tzOurTeam1Prev {
        left: -50px;
      }
      .tzOurTeam1Next {
        right: -50px;
      }
    }
  }
  .tzOurTeam2_Container {
    .tzOurTeamBox {
      .tzOurTeam2Prev {
        left: -50px;
      }
      .tzOurTeam2Next {
        right: -50px;
      }
    }
    .tzOurTeamItem {
      .tzOurTeam_Member {
        height: 221px;
        left: 46px;
        top: -141px;
        width: 221px;
      }
    }
  }
  .tzSinglePortfolio {
    .tzpost-pagenavi {
      a[rel="prev"] {
        left: 48px;
      }
      a[rel="next"] {
        right: 48px;
      }
    }
  }
  .page-template-template-landingpage {
    .tz-headerHome.tz-homeType1 {
      .tzHeaderMenu_nav {
        margin-right: 50px;
        padding-right: 50px;
      }
    }
    nav.pull-right {
      margin-right: 0;
    }
  }
}

@media (max-width: 1440px) {

  .tz_register_meetup {
    width: 80%;
  }
  .tz_recent_blog_meetup button.tz_recent_blog_pev_meetup {
    left: -90px;
  }
  .tz_recent_blog_meetup button.tz_recent_blog_next_meetup {
    right: -90px;
  }

}

@media (max-width: 1365px) {

  .tz_recent_blog_meetup button,
  .tz_meetup_slider_blog_single .tz_partner_blog_single_prev,
  .tz_meetup_slider_blog_single .tz_partner_blog_single_next {
    display: none;
  }
}

@media (max-width: 1280px) {
  .tz_home_slider_meetup .bx-viewport ul li {
    img {
      position: absolute;
      max-width: none;
      width: 100%;
      height: 100%;
    }
  }
}
/* Large desktop */
@media (min-width: 1200px) and (max-width: 1280px) {

  .tz_register_meetup {
    width: 95%;
  }

}

@media (max-width: 1200px) {
  .tz-headerHome.tz-homeType1 {
    .tzHeaderMenu_nav {
      margin-right: 0;
      padding-right: 0;
    }
  }
  span.tzNumberSection {
    font-size: 250px;
  }
  .tzPurchase {
    .tzPurchase-Image {
      img {
        max-width:940px;
      }
    }
  }
  .wpb_tabs {
    .wpb_tour_tabs_wrapper {
      ul.wpb_tabs_nav {
        li {
          a {
            padding: 12px 12px 12px 14px;
          }
        }
      }
    }
  }
  .wpb_content_element.wpb_tabs {
    .wpb_tour_tabs_wrapper {
      .wpb_tab {
        margin-right: 3px;
      }
    }
  }
  .tz-footer.tz-footer-type1 {
    .tz-backtotop {
      right:90px;
    }
  }
  .tz-blogSingle,.tz-blogDefault {
    padding-left: 15px;
    padding-right: 15px;
  }
  .tz-blogDefault {
    .tz-blogItemType2 {
      padding-left: 50px;
    }
  }
  .tz-sidebar {
    aside.widget_wpt {
      .wpt_widget_content {
        ul.wpt-tabs {
          li {
            a {
              padding: 10px 8px;
            }
          }
        }
      }
    }
  }
  .tz-blogMasonry {
    padding: 100px 30px;
  }

  /*meetup*/

  .tz_meetup_video_text {
    padding: 45px 35px 60px 0;
  }
  .tz_register_meetup {
    width: auto;
    margin: 0;
  }
  .tz_maniva_meetup_title.tz_maniva_meetup_title_type3 h3 {
    font-size: 14px;
  }
  .tz_slider_meetup_text {
    padding: 50px 0 50px 20px;
  }
  .tz-plazart-list li p.tz_list_item_content {
    width: 90%;
  }

}
/* Portrait tablet to landscape and desktop */

@media (min-width: 992px) and (max-width: 1199px){

  //meetup


  .tz-headerHome {
    a.tz_logo {
      padding: 25px 0 35px 0;
    }
  }
  .tz-homeType1 {
    border-right: none;
    border-left: none;
  }
  .tz-headerHome {
    nav {
      ul.tz-nav {
        > li {
          > a {
            font-size: 11px;
          }
          &:first-child {
            > a {
              padding-left: 0;
            }
          }
          &:last-child {
            > a {
              padding-right: 0;
            }
          }
        }
      }
    }
    &.tz-homeType2 {
      nav {
        ul.tz-nav {
          > li {
            padding: 0 10px;
            > a {
              font-size: 12px;
              padding: 30px 0;
            }
          }
        }
      }
      a.tz_logo {
        padding-right: 10px;
      }
    }
  }
  .tz-headerShop .tz-headerBottom nav > ul > li > a {
    font-size: 12px;
    padding: 0 7px;
  }
  .tzSection_title.tzSection_title_restaurant {
    padding: 0 50px;
    h3 {
      font-size: 30px;
    }
    .tz_section_restaurant_image {
      margin-bottom: 30px;
      margin-top: 0;
    }
  }

  .tz-footer.tz-footer-type1 aside.widget.MultiColorSubscribeWidget .multicolor-subscribe form.multicolor-subscribe-form input.mcolor-button {
    padding: 5px 20px;
    font-size: 11px;
  }
  .tz-blogDefault {
    padding-top: 50px;
    .tz-blogItem {
      .tz-blogContent {
        .tz-blogBox.tz-blogBoxQuote {
          .tz-blogQuote {
            background: #f8f8f9;
            &:after {
              display: none;
            }
            .tz-blogQuote-img {
              position: relative;
              &:after {
                position: absolute;
                background: rgba(36,39,50,0.5);
                content: '';
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
              }
            }
            .tz-blogQuote_detail {
              position: static;
              padding: 0 15px;
              p {
                font-size: 14px;
                color: #646569;
              }
              span.tz-blogQuote_author,
              i.fa {
                margin: 15px 0;
              }
            }
            .tz_blogQuote_information {
              position: static;
              padding: 0 15px;
              span.tzinfomation {
                small,
                a {
                  color: #95979d;
                }
              }
            }
            .tz_blogQuote_link {
              position: static;
              padding: 15px 15px 15px;
              a.tzreadmore {
                color: #646569;
              }
            }
          }
        }
      }
    }
    .wp-pagenavi {
      margin: 35px 0;
    }
  }

}


