/* 7.4 tz-video-meetup */
.tz_meetup_video_text {
  text-align: right;
  padding: 75px 45px 100px 0;
  h3 {
    font-size: 26px;
    margin-bottom: 12px;
    text-transform: uppercase;
    color: #ffffff;
    line-height: 1.4em;
    margin-bottom: 11px;
    em {
      font-weight: 300;
      font-style: normal;
    }
  }
  .tz_meetup_content {
    margin-top: 45px;
    p {
      line-height: 1.64em;
      color: #dddddd;
      strong {
        color: #eeeeee;
      }
    }
  }
}
.tz_video_meetup {
  position: relative;
  overflow: hidden;
  img {
    width: 100%;
    max-width: none;
    position: relative;
  }
  .tz_bk_video {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.6);
  }
  .tz_btn_play_video {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    a.tz_btn_easy {
      width: 95px;
      height: 95px;
      .border-radius(50%);
      border: 1px solid #e9e9eb;
      background: rgba(255,255,255,0.2);
      display: inline-block;
      i.fa {
        line-height: 90px;
        color: @color-theme;
        font-size: 30px;
        margin-left: 9px;
      }
    }
  }
}
a.tz_btn_video_meetup {
  font-weight: 700;
  text-decoration: none;
  border: 2px solid #fff;
  .border-radius(25px);
  color: #fff;
  display: inline-block;
  font-size: 12px;
  letter-spacing: 3px;
  margin-top: 58px;
  padding: 12px 35px;
  position: relative;
  transition: all 0.2s linear 0s;
  -moz-transition: all 0.2s linear 0s;
  -webkit-transition: all 0.2s linear 0s;
  -ms-transition: all 0.2s linear 0s;
  -o-transition: all 0.2s linear 0s;
  &:hover {
    color: #000000;
    background: #fff;
  }
  &.tz_meetup_btn_dark {
    color: @color-text-h2;
    border-color: @color-text-h2;
    &:hover {
      color: #fff;
      background: @color-text-h2;
    }
  }
  &.tz_meetup_bnt_orange {
    color: @color-theme;
    border-color: @color-theme;
    &:hover {
      color: #fff;
      background: @color-theme;
    }
  }
}