/* 7.5 Accordion */
.wpb_accordion.tzAccordion-Vertical {
  .wpb_accordion_wrapper {
    .wpb_accordion_section {
      margin:0 0 10px;
      h3.wpb_accordion_header {
        background:none repeat scroll 0 0 #323232;
        margin:0;
        span.ui-accordion-header-icon {
          background-image: url(images/accordion-right.png);
          background-repeat: no-repeat;
          display: block;
          height: 10px;
          position: absolute;
          right: 20px;
          top: 14px;
          width: 10px;
        }
        a {
          font-family: 'Roboto Slab',serif;
          font-size: 13px;
          font-weight: 400;
          line-height: 24px;
          color: #fefefe;
          text-transform: uppercase;
          padding: 7px 35px 7px 30px;
        }
      }
      h3.wpb_accordion_header.ui-state-active {
        background:none repeat scroll 0 0 @color-theme;
        span.ui-accordion-header-icon {
          background-image: url(images/accordion-bottom.png);
          top: 17px;
        }
      }
      .wpb_accordion_content {
        border-width:0 1px 1px;
        border-style:solid;
        border-color:#dddddd;
        padding: 11px 35px 10px 30px;
        p {
          color: #444444;
          font-family: "Raleway",sans-serif;
          font-size: 14px;
          font-weight: 300;
          line-height: 26px;
        }
      }
    }
  }
}

.wpb_accordion.tzAccordion-Toggle {
  .wpb_accordion_wrapper {
    .wpb_accordion_section {
      margin:0 0 10px;
      h3.wpb_accordion_header {
        background:none repeat scroll 0 0 #fafafa;
        border:1px solid #eeeeee;
        margin:0;
        a {
          font-family:'Lato',sans-serif;
          font-size: 14px;
          color:#666666;
          font-weight: 400;
          line-height: 28px;
          padding:4px 60px 6px 20px;
        }
        span {
          position: absolute;
          top:0;
          right: 0;
          width:40px;
          height: 100%;
          border-left: 1px solid #eeeeee;
          background-image: url(images/accordion-toggle.png);
          background-repeat: no-repeat;
          background-position: 12px 13px;
          background-color: #f1f1f1;
        }
      }
      h3.ui-accordion-header-active {
        span {
          background-position: 12px -25px;
        }
      }
      .wpb_accordion_content {
        padding: 18px 22px 8px;
        p {
          font-family: 'Lato',sans-serif;
          font-size: 14px;
          font-weight: 400;
          line-height: 24px;
          color: #656565;
        }
      }
    }
  }
}

