/* 7.2 Section title meetup */
.tz_maniva_meetup_title {
  h3 {
    font-size: 26px;
    margin-bottom: 12px;
    text-transform: uppercase;
    em {
      font-weight: 300;
      font-style: normal;
    }
    &.tz_meetup_title_raleway {
      font-family: @font-family-p;
      color: #ffffff;
      &.tz_title_meetup_thin {
        font-weight: 100;
      }
      &.tz_title_meetup_extra_light {
        font-weight: 200;
      }
      &.tz_title_meetup_normal {
        font-weight: 400;
      }
      &.tz_title_meetup_bold {
        font-weight: 700;
      }
      &.tz_title_meetup_semi_bold {
        font-weight: 600;
      }
    }
  }
  &.tz_text_align_center {
    text-align: center;
  }
  &.tz_text_align_left {
    text-align: left;
  }
  &.tz_text_align_right {
    text-align: right;
  }
  &.tz_maniva_meetup_title_type2 {
    h3 {
      color: #fff;
      margin-bottom: 9px;
    }
    &.tz_text_align_right {
      .tz_meetup_content {
        padding-left: 62px;
      }
    }
    &.tz_text_align_center {
      .tz_meetup_content {
        padding: 0;
      }
    }
    .tz_meetup_content {
      margin-top: 45px;
      p {
        line-height: 1.64em;
        color: #dddddd;
        strong {
          color: #eeeeee;
        }
        &:first-child {
          margin-bottom: 24px;
        }
      }
    }
  }
  &.tz_maniva_meetup_title_type3 {
    text-align: center;
    overflow: hidden;
    h3 {
      color: #f8f8f8;
      text-align: center;
      font-size: 24px;
      letter-spacing: 3px;
      position: relative;
      display: inline-block;
      padding: 0 45px;
      .tz_title_line_left {
        position: absolute;
        left: 100%;
        background: @color-theme;
        top: 50%;
        height: 1px;
        width: 100%;
      }
      .tz_title_line_right {
        position: absolute;
        right: 100%;
        background: @color-theme;
        top: 50%;
        height: 1px;
        width: 100%;
      }
    }
  }
}
.tz_image_title_meetup {
  margin-bottom: 30px;
  &.tz_image_title_meetup2 {
    margin-bottom: 0;
    margin-top: 27px;
  }
}
.tz_meetup_content {
  p {
    font-size: 16px;
    line-height: 1.6em;
  }
}
.tz_text_align_left .tz_meetup_content,
.tz_text_align_right .tz_meetup_content {
  padding: 0;
}
