/* 7.9 List */
.tz-plazart-list {
  margin:0;
  padding:0;
}
.tz-plazart-list li {
  list-style: none;
  font-size: 16px;
  color: @color-text-h2;
  padding: 12px 0 8px;
  font-weight: 400;
  line-height: 1.52em;
  &:before {
    display: table;
    content: '';
    clear: both;
  }
  span {
    &.tz_icon_maniva_list {
      display: inline-block;
      float: left;
      i {
        color: @color-theme-2;
      }
    }
  }
  p {
    &.tz_list_item_content {
      position: relative;
      margin-left: 30px;
      width: 53%;
      &:after {
        position: absolute;
        border-bottom: 1px dashed #dbd9d9;
        width: 100%;
        content: '';
        right: 0;
        bottom: -12px;
      }
    }
  }
}

