/* 4.1 page default */
.tzComments {
  position: relative;
  &:after {
    position: absolute;
    width: 100%;
    height: 1px;
    background: #dce2ed;
    bottom: 0;
    left: 0;
    content: '';
  }
    .comments-area {
      .comment-respond {
        padding: 0 0 62px;
        h3.comment-reply-title {
          font-family: @font-family-p;
          font-weight: 600;
          font-size: 30px;
          color: #3a424a;
          text-transform: capitalize;
          margin: 33px 0 28px 0;
        }
        form.comment-form {
          p.comment-notes {
            display: none;
          }
          p.logged-in-as {
            margin-bottom: 10px;
            a {
              color: @color-theme;
              margin-left: 5px;
            }
          }
          .form-comment-item {
            position: relative;
            margin-bottom: 20px;
            input.form-control {
              background: #f8f8f9;
              border: none;
              box-shadow: none;
              height: auto;
              padding: 12px;
              line-height: 1.5em;
              font-family: @font-family-lato;
              font-style: italic;
              color: #95979d;
            }
            textarea {
              background: #f8f8f9;
              border: none;
              box-shadow: none;
              height: auto;
              padding: 15px;
              line-height: 1.5em;
              font-family: @font-family-lato;
              font-style: italic;
              color: #95979d;
            }
            i.fa {
              position: absolute;
              top: 50%;
              right: 15px;
              font-size: 18px;
              color: #95979d;
              margin-top: -7px;
              &.fa-comment {
                top: 15px;
                margin-top: 0;
              }
            }
          }
          p.form-submit {
            input.submit {
              font-weight: 700;
              box-shadow: none;
              border: none;
              .border-radius(3px);
              font-size: 11px;
              padding: 13px 20px;
              color: #ffffff;
              background: #f57560;
              text-transform: uppercase;
              letter-spacing: 2px;
            }
          }
        }
      }
      .tz-Comment {
        margin: 0 0 77px 0;
        span.TzCommentTitle {
          display: inline-block;
          font-weight: 600;
          font-size: 30px;
          color: #3a424a;
          padding: 0 0 16px;
          margin-bottom: 43px;
        }
        ol.commentlist {
          margin: 0;
          padding: 0;
          li {
            list-style: none;
            article {
              background: #f8f8f8;
              margin-bottom: 26px;
              padding: 42px 20px 39px;
              .border-radius(5px);
              border-bottom: 5px solid rgba(0, 0, 0, 0.02);
              header {
                border: 4px solid #ffffff;
                background: #ffffff;
                display: inline-block;
                .border-radius(50%);
                overflow: hidden;
                img {
                  width: 98px;
                  height: 98px;
                  .border-radius(50%);
                }
              }
              .tz_comment_single_meetup {
                cite {
                  font-size: 18px;
                  font-weight: 600;
                  font-style: normal;
                  color: #3a424a;
                  display: block;
                }
                a.comments-datetime {
                  font-family: @font-family-lato;
                  font-size: 13px;
                  color: #95979d;
                  display: block;
                  margin-top: -3px;
                }
              }
              .tz-commentInfo {
                display: inline-block;
                a {
                  font-family: @font-family-lato;
                  font-size: 12px;
                  color: #ffffff;
                  text-decoration: none;
                  padding: 2px 15px;
                  background: #88939f;
                  .border-radius(5px);
                  text-transform: uppercase;
                  display: inline-block;
                  transition: all 0.4s ease 0s;
                  -webkit-transition: all 0.4s ease 0s;
                  -moz-transition: all 0.4s ease 0s;
                  -ms-transition: all 0.4s ease 0s;
                  -o-transition: all 0.4s ease 0s;
                  &.comment-edit-link {
                    margin-right: 8px;
                  }
                  &:hover {
                    background: #f57560;
                  }
                }
              }
              .comment {
                p {
                  margin-top: 7px;
                  color: #646569;
                }
              }
            }
            ol.children {
              border-left: 1px solid #f56048;
              padding-left: 55px;
              li {
                position: relative;
                &:before {
                  position: absolute;
                  content: '';
                  top: 50%;
                  left: -55px;
                  height: 1px;
                  width: 30px;
                  background: #f56048;
                  margin-top: -0.5px;
                }
                &:after {
                  position: absolute;
                  content: '';
                  width: 10px;
                  height: 10px;
                  .border-radius(50%);
                  background: #f56048;
                  top: 50%;
                  left: -25px;
                  margin-top: -5px;
                }
              }
            }
          }
        }
      }
    }
  }
