/* 7.10 Tabs */
.wpb_tabs {
  .tz_tabs_meetup {
    position: relative;
    &:before {
      position: absolute;
      content: '';
      left: 0;
      top: 50%;
      height: 1px;
      width: 16%;
      background: -moz-radial-gradient(center left, circle farthest-side, #ffffff 60%, @color-text-h2 100%);
      background: -o-radial-gradient(center left, circle farthest-side, #ffffff 60%, @color-text-h2 100%);
      background: -ms-radial-gradient(center left, circle farthest-side, #ffffff 60%, @color-text-h2 100%);
      background: -webkit-radial-gradient(center left, circle farthest-side, #ffffff 60%, @color-text-h2 100%);
      z-index: 1;
    }
    &:after {
      position: absolute;
      content: '';
      right: 0;
      top: 50%;
      height: 1px;
      width: 16%;
      background: -moz-radial-gradient(center right, circle farthest-side, #ffffff 60%, @color-text-h2 100%);
      background: -o-radial-gradient(center right, circle farthest-side, #ffffff 60%, @color-text-h2 100%);
      background: -ms-radial-gradient(center right, circle farthest-side, #ffffff 60%, @color-text-h2 100%);
      background: -webkit-radial-gradient(center right, circle farthest-side, #ffffff 60%, @color-text-h2 100%);
      z-index: 1;
    }
  }
  .wpb_tour_tabs_wrapper {
    ul.wpb_tabs_nav.tz_meetup_tabs {
      position: relative;
      &:after {
        position: absolute;
        top: 50%;
        width: 100%;
        left: 0;
        content: '';
        height: 1px;
        background: @color-text-h2;
      }
      li {
        a {
          font-family: @font-family-h3;
          font-size: 11px;
          font-weight: 400;
          color: @color-text-h2;
          text-transform: uppercase;
          text-shadow: none;
          position:relative;
          padding: 6px 30px;
          .border-radius (25px);
          border: 1px solid @color-text-h2;
        }
        &.ui-tabs-active {
          a {
            background: @color-theme;
            border-color: @color-theme;
            color: #ffffff;
          }
        }
      }
    }
  }
  .wpb_tour_tabs_wrapper.tzTabs_center {
    margin-top: 95px;
    ul.wpb_tabs_nav.tz_meetup_tabs {
      text-align: center;
      li {
        &:hover {
          z-index: 1;
        }
        display: inline-block;
        float: none;
        background: none;
        margin:0 1px;
        a {
          background: none;
          border:1px solid @color-theme;
          color:@color-theme;
        }
      }
    }
  }
  ul.wpb_tabs_nav.tz_meetup_tabs {
    li {
      margin:0 5px 1px 0;
      float: none;
      display: inline-block;
      position: relative;
      z-index: 2;
      &:hover {
        background-color: #ffffff;
      }
      &.ui-tabs-active {
        background-color: transparent;
      }
    }
  }
}
.wpb_tabs .wpb_tour_tabs_wrapper.tzTabs_left ul.wpb_tabs_nav li a:hover,
.wpb_tabs .wpb_tour_tabs_wrapper.tzTabs_left ul.wpb_tabs_nav li.ui-tabs-active a {
  background:none repeat scroll 0 0 @color-theme;
}
.wpb_content_element.wpb_tabs {
  .wpb_tour_tabs_wrapper.tz_meetup_wpb_tour_tabs {
    .wpb_tab {
      margin-top: 70px;
      margin-right: 2px;
      padding: 0;
      background: transparent;
    }
  }
  .wpb_tour_tabs_wrapper.tzTabs_center {
    .wpb_tab {
      background:none!important;
      border:none;
      p {
        margin-bottom:30px;
        font-weight:400;
      }
    }
  }
  p {
    color: @color-theme;
    font-size: 14px;
  }
}