/* 7.17 tz_register_meetup */
.tz_register_meetup {
  width: 60%;
  margin: 0 0 0 auto;
}
.tz_register_meetup_pricing_item {
  .border-radius (5px);
  position: relative;
  cursor: pointer;
  margin-bottom: 15px;
  -moz-transition: all 0.3s linear 0s;
  -webkit-transition: all 0.3s linear 0s;
  -ms-transition: all 0.3s linear 0s;
  -o-transition: all 0.3s linear 0s;
  &:after {
    clear: both;
    content: '';
    display: table;
  }
  &:hover {
    .tz_register_meetup_pricing_item_container {
      background: @color-theme;
      h3,p {
        color: #ffffff;
      }
    }
    .tz_register_meetup_pricing_item_price {
      &:after {
        position: absolute;
        top: 50%;
        right: -15px;
        content: '';
        border-bottom: 25px solid transparent;
        border-top: 25px solid transparent;
        border-left: 25px solid @color-theme;
        margin-top: -22px;
        z-index: -1;
      }
    }
  }
  &.active {
    .tz_register_meetup_pricing_item_container {
      background: @color-theme;
      h3,p {
        color: #ffffff;
      }
    }
    .tz_register_meetup_pricing_item_price {
      &:after {
        position: absolute;
        top: 50%;
        right: -15px;
        content: '';
        border-bottom: 25px solid transparent;
        border-top: 25px solid transparent;
        border-left: 25px solid @color-theme;
        margin-top: -22px;
        z-index: -1;
      }
    }
  }
}
.tz_register_meetup_pricing {
  margin-right: 12%;
  .tz_register_meetup_pricing_item:last-child {
    margin-bottom: 0;
  }
}
.tz_register_meetup_pricing_item_container {
  padding: 21px 20px;
  width: 70%;
  float: left;
  background: #ffffff;
  transition: all 0.3s linear 0s;
  -moz-transition: all 0.3s linear 0s;
  -webkit-transition: all 0.3s linear 0s;
  -ms-transition: all 0.3s linear 0s;
  -o-transition: all 0.3s linear 0s;
  .border-radius ( 5px 0 0 5px );
  h3 {
    color: #5c5e63;
    font-size: 20px;
    font-weight: 300;
    margin-bottom: 10px;
  }
  p {
    line-height: 1.6em;
  }
}
.tz_register_meetup_pricing_item_price {
  width: 30%;
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  background-image: url("images/price-pay.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: @color-theme;
  .border-radius ( 0 5px 5px 0 );
  h3 {
    color: #ffffff;
    font-size: 40px;
  }
}
.tz_register_meetup_form_pay {
  padding: 0 0 0 66px;
  h3 {
    color: #ffffff;
    font-size: 26px;
    text-transform: uppercase;
    line-height: 2em;
    em {
      font-weight: 100;
      font-style: normal;
    }
  }
  .tz_register_meetup_title_under {
    display: inline-block;
    width: 25px;
    height: 1px;
    background: @color-theme;
    margin: 13px 0 48px 0;
  }
  p {
    color: #b1b4bc;
    font-size: 16px;
    line-height: 1.6em;
  }
}
.tz_register_meetup_form_content {
  margin-top: 50px;
  input[type="submit"] {
    font-weight: 700;
    font-size: 12px;
    color: #ffffff;
    background: transparent;
    padding: 13px 35px;
    display: inline-block;
    letter-spacing: 3px;
    .border-radius ( 25px );
    text-transform: uppercase;
    margin-top: 45px;
    border: 2px solid #ffffff;
  }
}
.tz_register_meetup_form_input {
  margin-bottom: 15px;
  h4 {
    font-weight: 700;
    font-size: 13px;
    color: #7e8394;
    text-transform: uppercase;
    margin-bottom: 6px;
    span {
      color: @color-theme;
    }
  }
  input {
    width: 100%;
    padding: 13px 20px;
    background: #ffffff;
    border: 2px solid #ffffff;
    .border-radius ( 5px );
    color: #53586b;
    font-size: 15px;
    transition: all 0.3s linear 0s;
    -moz-transition: all 0.3s linear 0s;
    -webkit-transition: all 0.3s linear 0s;
    -ms-transition: all 0.3s linear 0s;
    -o-transition: all 0.3s linear 0s;
    &:focus {
      border: 2px solid @color-theme;
    }
  }
}