/* 7.6 Latest Blog meetup */
.tz_meetup_slider_blog {
  cursor: w-resize;
}
.tz_meetup_btn_post {
  text-align: center;
  margin-top: 98px;
  a {
    border: 2px solid #000000;
    border-radius: 25px;
    color: #242732;
    display: inline-block;
    font-size: 12px;
    font-weight: 600;
    letter-spacing: 3px;
    padding: 13px 35px;
    text-decoration: none;
    text-transform: uppercase;
    transition: all 0.2s linear 0s;
    -moz-transition: all 0.2s linear 0s;
    -webkit-transition: all 0.2s linear 0s;
    -ms-transition: all 0.2s linear 0s;
    -o-transition: all 0.2s linear 0s;
    &:hover {
      background: #000000 none repeat scroll 0 0;
      color: #ffffff;
    }
  }
}
.tz_recent_blog_meetup_content {
  padding: 0 15px;
  &:hover {
    .tz_meetup_slider_img:before,
    .tz_meetup_post_img:before {
      background: rgba(0,0,0,0.62);
    }
    .tz_meetup_view_link {
      span {
        a {
          transform: scale(1);
          -moz-transform: scale(1);
          -webkit-transform: scale(1);
          -ms-transform: scale(1);
          -o-transform: scale(1);
          opacity: 1;
        }
      }
    }
  }
}
.tz_recent_blog_meetup {
  position: relative;
  button {
    background: transparent;
    border: none;
    position: absolute;
    color: #b0b2b6;
    width: 40px;
    height: 40px;
    .border-radius (50%);
    border: 1px solid #b0b2b6;
    font-size: 32px;
    &.tz_recent_blog_pev_meetup {
      left: -125px;
      top: 50%;
      padding: 0 3px 3px 0;
      transition: all 0.2s linear 0s;
      -moz-transition: all 0.2s linear 0s;
      -webkit-transition: all 0.2s linear 0s;
      -ms-transition: all 0.2s linear 0s;
      -o-transition: all 0.2s linear 0s;
      &:hover {
        background: @color-theme;
        color: #ffffff;
        border: 1px solid @color-theme;
      }
    }
    &.tz_recent_blog_next_meetup {
      right: -125px;
      top: 50%;
      padding: 0 0 3px 3px;
      transition: all 0.2s linear 0s;
      -moz-transition: all 0.2s linear 0s;
      -webkit-transition: all 0.2s linear 0s;
      -ms-transition: all 0.2s linear 0s;
      -o-transition: all 0.2s linear 0s;
      &:hover {
        background: @color-theme;
        color: #ffffff;
        border: 1px solid @color-theme;
      }
    }
  }
}
.tz_meetup_slider_img {
  position: relative;
  overflow: hidden;
  .border-radius (5px);
  ul.slides {
    li {
      display: none;
      &:first-child {
        display: block;
      }
    }
  }
  img {
    width: 100%;
  }
  &:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.3);
    left: 0;
    top: 0;
    z-index: 1;
    transition: all 0.2s linear 0s;
    -moz-transition: all 0.2s linear 0s;
    -webkit-transition: all 0.2s linear 0s;
    -ms-transition: all 0.2s linear 0s;
    -o-transition: all 0.2s linear 0s;
  }
  .flex-direction-nav {
    a {
      color: #ffffff;
      &:before {
        font-size: 20px;
        width: 32px;
        height: 32px;
        .border-radius(5px);
        background: rgba(255,255,255,0.2);
        padding: 5px 10px;
      }
    }
  }
  .flex-direction-nav .flex-prev {
    right: 46px;
    opacity: 1;
    left: auto;
    top: auto;
    bottom: 12px;
  }
  .flex-direction-nav .flex-next {
    right: 20px;
    opacity: 1;
    top: auto;
    bottom: 12px;
  }
}
.tz_meetup_post_img {
  position: relative;
  overflow: hidden;
  .border-radius (5px);
  &:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.3);
    left: 0;
    top: 0;
    transition: all 0.2s linear 0s;
    -moz-transition: all 0.2s linear 0s;
    -webkit-transition: all 0.2s linear 0s;
    -ms-transition: all 0.2s linear 0s;
    -o-transition: all 0.2s linear 0s;
  }
  img {
    width: 100%;
  }
}
.tz_recent_blog_meetup_detail {
  h4 {
    font-size: 15px;
    text-transform: uppercase;
    margin: 25px 0 20px;
    a {
      color: @color-text-h2;
      text-decoration: none;
    }
  }
  .tz_meetup_description_latest {
    line-height: 1.7em;
  }
}
span.tz_meetup_infomation {
  font-style: italic;
  display: block;
  margin-top: 12px;
  a {
    color: #95979d;
    text-decoration: none;
    font-size: 12px;
    margin-right: 10px;
  }
  small {
    font-size: 12px;
    margin-right: 10px;
    i.fa {
      margin-right: 5px;
    }
  }
}
.tz_image_recent_blog_meetup {
  position: relative;
  cursor: pointer;
  .tz_recent_blog_meetup_date {
    position: absolute;
    top: 0;
    left: 0;
    background: @color-theme;
    z-index: 1;
    padding: 13px 15px;
    text-align: center;
    .border-top-left-radius (5px);
    .border-bottom-right-radius (5px);
    span {
      display: block;
      font-family: 'Lato', sans-serif;
      &.tz_month_latest {
        color: #ffffff;
        text-transform: uppercase;
        font-size: 20px;
      }
      &.tz_date_latest {
        color: #e5c9bb;
        font-size: 17px;
      }
    }
  }
}
.tz_meetup_view_link {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
  span {
    a {
      background: rgba(255, 255, 255, 0.2);
      .border-radius (5px);
      color: #ffffff;
      display: inline-block;
      font-size: 18px;
      margin: 1px;
      padding: 8px;
      text-decoration: none;
      opacity: 0;
      transform: scale(0.5);
      -moz-transform: scale(0.5);
      -webkit-transform: scale(0.5);
      -ms-transform: scale(0.5);
      -o-transform: scale(0.5);
      transition: all 0.3s linear 0s;
      -moz-transition: all 0.3s linear 0s;
      -webkit-transition: all 0.3s linear 0s;
      -ms-transition: all 0.3s linear 0s;
      -o-transition: all 0.3s linear 0s;
    }
  }
}