/* 7.19 tz-counter */
.tz-counter {
  text-align: center;
}
.tzCounter_type {
  .tz-counter-font {
    display: block;
    margin-bottom: 25px;
    i.fa {
      margin-bottom: 10px;
      color: @color-theme;
      font-size: 36px;
    }
  }
  .tz-counter-image {
    margin-bottom: 35px;
    height:35px;
  }
  .tz-counter-number {
    display: block;
    margin-bottom: 10px;
    em.stat-count {
      font-family: @font-family-h3;
      font-weight: 100;
      font-size: 64px;
      color:@color-text-h2;
      font-style: normal;
    }
  }
  p.tz-counter-title {
    font-family: @font-family-p;
    font-weight: 800;
    color:@color-text-h2;
  }
}

