/* 7.15 our Team meetup */
.tz_meetup_our_team_container {
  &:after {
    display: table;
    clear: both;
    content: '';
  }
  .tz_meetup_our_team_thumbnail {
    float: left;
  }
  &.tz_meetup_our_team_column_2 {
    .tz_meetup_our_team_thumbnail {
      width: 50%;
    }
  }
  &.tz_meetup_our_team_column_3 {
    .tz_meetup_our_team_thumbnail {
      width: 33.3333%;
    }
  }
  &.tz_meetup_our_team_column_4 {
    .tz_meetup_our_team_thumbnail {
      width: 25%;
    }
  }
}
.tz_meetup_our_team_text {
  text-align: right;
  padding: 75px 45px 100px 0;
  h3 {
    font-size: 26px;
    text-transform: uppercase;
    color: #ffffff;
    line-height: 1.4em;
    margin-bottom: 11px;
    em {
      font-weight: 300;
      font-style: normal;
    }
  }
  .tz_meetup_content {
    margin-top: 45px;
    p {
      line-height: 1.64em;
      color: #dddddd;
      margin-bottom: 24px;
      strong {
        color: #eeeeee;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
.tz_meetup_our_team_thumbnail {
  position: relative;
  overflow: hidden;
  &:after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.2);
    transition: all 0.4s linear 0.1s;
    -moz-transition: all 0.4s linear 0.1s;
    -webkit-transition: all 0.4s linear 0.1s;
    -ms-transition: all 0.4s linear 0.1s;
    -o-transition: all 0.4s linear 0.1s;
  }
  &:hover {
    &:after {
      background: rgba(0,0,0,0.7);
    }
  }
  img {
    transition: all 0.4s linear 0s;
    -moz-transition: all 0.4s linear 0s;
    -webkit-transition: all 0.4s linear 0s;
    -ms-transition: all 0.4s linear 0s;
    -o-transition: all 0.4s linear 0s;
    width: 100%;
    max-width: none;
    position: relative;
  }
  &:hover {
    img {
      transition: all 0.4s linear 0s;
      -moz-transition: all 0.4s linear 0s;
      -webkit-transition: all 0.4s linear 0s;
      -ms-transition: all 0.4s linear 0s;
      -o-transition: all 0.4s linear 0s;
      transform: scale(1.2);
      -moz-transform: scale(1.2);
      -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
      -o-transform: scale(1.2);
    }
  }
  .tz_member_meetup {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    h4.tz_meetup_employment {
      font-weight: 600;
      font-size: 11px;
      color: #ffffff;
      letter-spacing: 1px;
      transform: translateY(30px);
      -moz-transform: translateY(30px);
      -webkit-transform: translateY(30px);
      -ms-transform: translateY(30px);
      -o-transform: translateY(30px);
      opacity: 0;
      transition: all 0.2s ease 0s;
      -moz-transition: all 0.2s ease 0s;
      -webkit-transition: all 0.2s ease 0s;
      -ms-transition: all 0.2s ease 0s;
      -o-transition: all 0.2s ease 0s;
    }
    h3.tz_meetup_name {
      font-weight: 400;
      color: #ffffff;
      font-size: 18px;
      letter-spacing: 1px;
      transform: translateY(30px);
      -moz-transform: translateY(30px);
      -webkit-transform: translateY(30px);
      -ms-transform: translateY(30px);
      -o-transform: translateY(30px);
      opacity: 0;
      transition: all 0.2s ease 0s;
      -moz-transition: all 0.2s ease 0s;
      -webkit-transition: all 0.2s ease 0s;
      -ms-transition: all 0.2s ease 0s;
      -o-transition: all 0.2s ease 0s;
    }
    .tz_meetup_social {
      margin-top: 10px;
      transform: translateY(30px);
      -moz-transform: translateY(30px);
      -webkit-transform: translateY(30px);
      -ms-transform: translateY(30px);
      -o-transform: translateY(30px);
      opacity: 0;
      transition: all 0.4s ease 0s;
      -moz-transition: all 0.4s ease 0s;
      -webkit-transition: all 0.4s ease 0s;
      -ms-transition: all 0.4s ease 0s;
      -o-transition: all 0.4s ease 0s;
      a {
        text-decoration: none;
        i.fa {
          color: #ffffff;
          font-size: 16px;
          padding: 0 10px;
          transition: all 0.2s ease 0s;
          -moz-transition: all 0.2s ease 0s;
          -webkit-transition: all 0.2s ease 0s;
          -ms-transition: all 0.2s ease 0s;
          -o-transition: all 0.2s ease 0s;
          &:hover {
            color: @color-theme;
          }
        }
      }
    }
  }
  &:hover {
    h4.tz_meetup_employment {
      transition: all 0.4s ease 0.2s;
      -moz-transition: all 0.4s ease 0.2s;
      -webkit-transition: all 0.4s ease 0.2s;
      -ms-transition: all 0.4s ease 0.2s;
      -o-transition: all 0.4s ease 0.2s;
      opacity: 1;
      transform: translateY(0);
      -moz-transform: translateY(0);
      -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
      -o-transform: translateY(0);
    }
  }
  &:hover {
    h3.tz_meetup_name {
      transition: all 0.4s ease 0.35s;
      -moz-transition: all 0.4s ease 0.35s;
      -webkit-transition: all 0.4s ease 0.35s;
      -ms-transition: all 0.4s ease 0.35s;
      -o-transition: all 0.4s ease 0.35s;
      opacity: 1;
      transform: translateY(0);
      -moz-transform: translateY(0);
      -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
      -o-transform: translateY(0);
    }
  }
  &:hover {
    .tz_meetup_social {
      opacity: 1;
      transition: all 0.4s ease 0.5s;
      -moz-transition: all 0.4s ease 0.5s;
      -webkit-transition: all 0.4s ease 0.5s;
      -ms-transition: all 0.4s ease 0.5s;
      -o-transition: all 0.4s ease 0.5s;
      transform: translateY(0);
      -moz-transform: translateY(0);
      -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
      -o-transform: translateY(0);
    }
  }
}