/* 4.3 Blog Masonry */
.tz-blogMasonry {
  padding:100px 0;
  .tzBlogmasonry {
    margin-right: -30px;
    overflow: visible!important;
    .blogMasonry-item {
      &.tz-grid-4 {
        width: 25%;
        float: left;
      }
      &.tz-grid-3 {
        width: 33.33333333%;
        float: left;
      }
      &.tz-grid-2 {
        width: 50%;
        float: left;
      }
      .flex-viewport {
        width:100%;
      }
      .tz-blogInner {
        margin:0 30px 30px 0;
        box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);
        .tz-BlogImage {
          position: relative;
          img {
            width:100%;
            height: auto;
          }
          .tz-ImageOverlay {
            background-color: rgba(0, 0, 0, 0);
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            transition: all 0.3s ease-in 0s;
            -moz-transition: all 0.3s ease-in 0s;
            -webkit-transition: all 0.3s ease-in 0s;
            -webkit-transition: all 0.3s ease-in 0s;
            -o-transition: all 0.3s ease-in 0s;
          }
          a {
            position: absolute;
            top:30%;
            width:40px;
            height:40px;
            background-color:#fff;
            margin-top: -20px;
            text-align: center;
            border-radius: 50%;
            -webkit-border-radius: 50%;
            -ms-border-radius: 50%;
            -moz-border-radius: 50%;
            -o-border-radius: 50%;
            visibility: hidden;
            opacity: 0;
            i {
              color:#000;
              padding:14px 0;
            }
          }
          a.tz-ViewImage {
            left: 50%;
            margin-left: -44px;
          }
          a.tz-ViewDetail {
            right: 50%;
            margin-right: -44px;
          }
          a:hover {
            i {
              color:#fff;
            }
          }
        }
        .tz-BlogImage:hover {
          .tz-ImageOverlay {
            background-color: rgba(0, 0, 0, 0.25);
          }
          a.tz-ViewImage {
            top: 50%;
            visibility: visible;
            opacity: 1;
            transition: all 0.3s ease-in 0s;
            -moz-transition: all 0.3s ease-in 0s;
            -webkit-transition: all 0.3s ease-in 0s;
            -ms-transition: all 0.3s ease-in 0s;
            -o-transition: all 0.3s ease-in 0s;
          }
          a.tz-ViewDetail {
            top: 50%;
            visibility: visible;
            opacity: 1;
            transition: all 0.3s ease-in 0.1s;
            -moz-transition: all 0.3s ease-in 0.1s;
            -webkit-transition: all 0.3s ease-in 0.1s;
            -ms-transition: all 0.3s ease-in 0.1s;
            -o-transition: all 0.3s ease-in 0.1s;
          }
        }
        .tz-blogDate{
          position: absolute;
          top:0;
          left: 0;
          z-index: 999;
          .tz-DateText{
            width: 50px;
            height: 50px;
            background: @color-theme;
            padding:5px;
            .tz-TextDay{
              display: block;
              font-family: 'Lato',sans-serif;
              font-weight: 400;
              font-size: 18px;
              line-height: 24px;
              color: #ffffff;
              text-align: center;
            }
            .tz-TextMonth{
              display: block;
              font-family: 'Lato',sans-serif;
              font-weight: 400;
              font-size: 13px;
              line-height: 15px;
              color: #ffffff;
              text-align: center;
              text-transform: uppercase;
            }
          }
          .tz-blogIcon{
            width: 50px;
            height: 50px;
            background: #444444;
            text-align: center;
            i{
              font-size: 16px;
              color: #fff;
              padding: 16px;
            }
          }
        }
        .tz-blogSlider {
          position: relative;
          ul.slides {
            li {
              img {
                width: 100%;
                height: auto;
              }
            }
          }
          ol.flex-control-nav{
            bottom: 0;
            li {
              margin:0 3px;
              a {
                background: #fff;
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                -ms-border-radius: 50%;
                -o-border-radius: 50%;
                box-shadow: none;
                height: 12px;
                width: 12px;
                &.flex-active{
                  background: @color-theme;
                }
              }
            }
          }
        }
        .tz-blogVideo {
          position: relative;
          height: 230px;
          iframe.iframe-full {
            width:100%;
            height: 230px;
          }
        }
        .tz-blogAudio {
          position: relative;
          height: 150px;
        }
        .tz-blogInfo {
          border:1px solid #dddddd;
          padding:15px 18px 20px;
          border-bottom: 2px solid @color-theme;
          h4.title {
            margin:0;
            padding:0;
            a {
              font-family: 'Roboto Slab',serif;
              font-weight: 400;
              font-size: 15px;
              line-height: 18px;
              color:#111111;
            }
            a:hover {
              text-decoration: none;
            }
          }
          .tzinfomation {
            display: block;
            margin-bottom: 12px;
            small {
              i {
                padding-right: 5px;
              }
            }
            a:hover {
              text-decoration: none;
            }
          }
          p {
            font-family: 'Raleway',sans-serif;
            font-size: 14px;
            line-height: 24px;
            font-weight: 300;
            color:#444444;
            margin: 0 0 15px;
          }
          a.tzreadmore {
            color: #42484d;
            display:block;
            font-family: "Roboto Slab",serif;
            font-size: 11px;
            font-weight: 400;
            line-height: 24px;
            text-decoration: none;
            text-transform: uppercase;
            text-align: right;
            letter-spacing: 2px;
            i{
              margin-left: 10px;
            }
            &:hover{
              text-decoration: none;
              color: @color-theme;
            }
          }
        }
      }
    }
  }
  .wp-pagenavi {
    display:block;
    text-align: center;
    margin: 91px 0 29px;
  }
  .iframe-full-audio {
    height: 150px;
  }
}
.tz-blogMasonry .tzBlogmasonry .blogMasonry-item .tz-blogInner .tz-BlogImage a:hover span,
.tz-blogMasonry .tzBlogmasonry .blogMasonry-item .tz-blogInner .tz-BlogImage a:hover {
  background-color: @color-theme;
}
.tz-blogMasonry .tzBlogmasonry .blogMasonry-item .tz-blogInner .tz-blogSlider iframe,
.tz-blogMasonry .tzBlogmasonry .blogMasonry-item .tz-blogInner .tz-blogAudio iframe,
.tz-blogMasonry .tzBlogmasonry .blogMasonry-item .tz-blogInner .tz-blogVideo iframe {
  border:none;
  border-radius: 0;
}
.tz-blogMasonry .tzBlogmasonry .blogMasonry-item .tz-blogInner .tz-blogInfo .tzinfomation small,
.tz-blogMasonry .tzBlogmasonry .blogMasonry-item .tz-blogInner .tz-blogInfo .tzinfomation a {
  font-family: 'Raleway',sans-serif;
  font-weight: 300;
  font-size:12px;
  font-style: italic;
  line-height: 20px;
  color:#79797e;
  margin-right: 5px;
}
.tz-blogMasonry .wp-pagenavi a,.tz-blogMasonry .wp-pagenavi span {
  font-family: 'Raleway',sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color:#444444;
  padding:8px 14px;
  border:1px solid #dddddd;
}
.tz-blogMasonry .wp-pagenavi a:hover,.tz-blogMasonry .wp-pagenavi span.current {
  border:1px solid @color-theme;
  background-color: @color-theme;
  color:#ffffff;
  text-decoration: none;
}
