/* 2.1 - Header Page Blog + Portfolio */
.tz-header {
  .tz-headerTop {
    border-bottom: 1px solid #e6e6e6;
    .tz-headerLeft {
      ul {
        margin:0;
        padding:0;
        li {
          list-style: none;
          float: left;
          margin-right: 18px;
          padding:6px 0;
          font-family: 'Raleway', sans-serif;
          font-size: 12px;
          font-weight: 400;
          color:#555555;
          i {
            padding-right: 5px;
            color:#aaaaaa;
          }
        }
        li:last-child {
          margin-right: 0;
        }
      }
      ul:after {
        content:"";
        display: block;
        clear: both;
      }
    }
    .tz-headerRight {
      ul {
        margin:0;
        padding:0;
        li {
          list-style: none;
          float: left;
          a {
            font-family: 'Raleway', sans-serif;
            font-size: 14px;
            font-weight: normal;
            color:#555555;
            i {
              color:#aaaaaa;
              padding:11px 6px;
            }
            &:hover i{
              color:@color-theme;
            }
          }
        }
      }
      ul:after {
        content:"";
        display: block;
        clear: both;
      }
    }
  }
  .tz-headerBottom {
    border-bottom: 3px solid @color-theme;
    a.tz_logo {
      padding:32px 0;
    }
    nav {
      ul {
        margin:0;
        padding:0;
        li {
          position: relative;
        }
      }
      & > ul {
        & > li {
          & > a {
            padding:0 13px;
            margin: 40px 0;
            font-family: 'Raleway', sans-serif;
            font-weight: 400;
            font-size: 13px;
            text-transform: uppercase;
            color:#666666;
            line-height: 11px;
            border-right: 1px solid #c2c1c1;
          }
          & > a:hover {
            background: none;
            color:@color-theme;
          }
        }
        & > li:last-child {
          & > a {
            padding-right: 0;
            border-right: none;
          }
        }
      }
    }
  }
}
.tz_icon_menu {
  display: none;
}