/* 7.8 tz-slider-meetup */
.tz_slider_meetup {
  position: relative;
  overflow: hidden;
  .tz_meetup_slider {
    overflow: hidden;
    position: relative;
    &:after {
      position: absolute;
      width: 50%;
      top: 0;
      right: 0;
      content: '';
      height: 100%;
      background: rgba(255,255,255,0.9);
    }
    &.tz_meetup_slider_block_hide {
      &:after {
        background: transparent;
      }
    }
  }
  .tz_custom_images_slider {
    cursor: w-resize;
    overflow: hidden;
    position: relative;
    img {
      max-width: none;
      position: relative;
    }
    .tz-bk-slider-meetup {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,0.48);
    }
  }
  .owl-theme {
    .owl-controls {
      position: absolute;
      bottom: 20px;
      right: 52%;
      margin: 0;
      .owl-page {
        &.active {
          span {
            background: @color-theme;
          }
        }
        span {
          background: transparent none repeat scroll 0 0;
          border: 2px solid @color-theme;
          height: 15px;
          width: 15px;
          opacity: 1;
          margin-right: 0;
          &.owl-numbers {
            width: 25px;
            height: 25px;
            padding: 1px 0;
            line-height: 1.3em;
          }
        }
      }
    }
  }
}
.tz_slider_meetup_text {
  position: absolute;
  top: 0;
  right: 0;
  width: 50%;
  padding: 74px 0 77px 83px;
  z-index: 1;
  h3 {
    font-weight: 500;
    font-size: 26px;
    margin-bottom: 7px;
    em {
      font-weight: 300;
      font-style: normal;
    }
  }
  .tz_line_slider {
    display: inline-block;
    width: 25px;
    height: 1px;
    background: @color-text-h2;
  }
  .tz_slider_content {
    margin-top: 48px;
    p {
      font-size: 16px;
      line-height: 1.6em;
      color: @color-text-h2;
    }
  }
  a.tz_btn_video_meetup {
    border: 2px solid @color-text-h2;
    color: @color-text-h2;
    line-height: 1.7em;
    margin-top: 52px;
    padding: 12px 35px;
    &:hover {
      background: @color-text-h2;
      color: #ffffff;
    }
  }
}
