/* 7.11 Tour */
.wpb_tour {
  .wpb_tour_tabs_wrapper {
    ul.wpb_tabs_nav {
      li {
        a {
          position: relative;
          display: inline-block;
          font-family: 'Roboto Slab',serif;
          font-size: 12px;
          font-weight: 400;
          color:#ffffff;
          line-height: 20px;
          text-transform: uppercase;
          text-align: center;
          padding:10px;
          background-color: #323232;
          transition: all 0.3s ease-in-out 0s;
          -webkit-transition: all 0.3s ease-in-out 0s;
          -moz-transition: all 0.3s ease-in-out 0s;
          -ms-transition: all 0.3s ease-in-out 0s;
          -o-transition: all 0.3s ease-in-out 0s;
        }
        a:after {
          content:'';
          position: absolute;
          top:50%;
          margin-top: -9px;
          right: -8px;
          border-top: 9px solid transparent;
          border-left: 8px solid @color-theme;
          border-bottom: 9px solid transparent;
          display: none;
          transition: all 0.3s ease-in-out 0s;
          -webkit-transition: all 0.3s ease-in-out 0s;
          -moz-transition: all 0.3s ease-in-out 0s;
          -ms-transition: all 0.3s ease-in-out 0s;
          -o-transition: all 0.3s ease-in-out 0s;
        }
      }
    }
    .wpb_tab {
      border:1px solid #dddddd!important;
      padding:15px 21px 10px!important;
      margin-left: -1px;
      p {
        color: #444444;
        font-family: "Raleway",sans-serif;
        font-size: 14px;
        font-weight: 300;
        line-height: 26px;
      }
    }
    .wpb_tour_next_prev_nav {
      padding:0;
      span {
        a {
          font-size: 12px;
          font-weight: 400;
          font-style: italic;
          color: #444444;
          text-decoration: none;
        }
        a:hover {
          color: @color-theme;
        }
      }
    }
  }
}
.wpb_tour .wpb_tour_tabs_wrapper ul.wpb_tabs_nav li a:hover,.wpb_tour .wpb_tour_tabs_wrapper ul.wpb_tabs_nav li.ui-tabs-active a {
  background-color: @color-theme;
}
.wpb_tour .wpb_tour_tabs_wrapper ul.wpb_tabs_nav li a:hover:after,.wpb_tour .wpb_tour_tabs_wrapper ul.wpb_tabs_nav li.ui-tabs-active a:after {
  display: block;
}
