/* 7.7 tz-button-meetup */
.tz_meetup_btn {
  h4.tz_meetup_title_btn {
    color: #ffffff;
    font-size: 32px;
    text-transform: uppercase;
    margin-bottom: 35px;
  }
  a {
    text-decoration: none;
    color: @color-text-h2;
    font-weight: 700;
    font-size: 12px;
    letter-spacing: 3px;
    display: inline-block;
    padding: 13px 35px;
    .border-radius(25px);
    border: 2px solid #000000;
    transition: all 0.2s linear 0s;
    -moz-transition: all 0.2s linear 0s;
    -webkit-transition: all 0.2s linear 0s;
    -ms-transition: all 0.2s linear 0s;
    -o-transition: all 0.2s linear 0s;
    &:hover {
      background: #000000;
      color: #ffffff;
    }
    &.tz_meetup_btn_white {
      color: #ffffff;
      border-color: #ffffff;
      &:hover {
        background: #ffffff;
        color: #000000;
      }
    }
    &.tz_meetup_bnt_orange {
      color: @color-theme;
      border-color: @color-theme;
      &:hover {
        background: @color-theme;
        color: #ffffff;
      }
    }
    &.tz_meetup_bnt_orange_bk {
      background: @color-theme;
      color: #ffffff;
      border-color: @color-theme;
    }
    i.fa {
      font-size: 15px;
    }
  }
}