/**
 * 9.0 Sidebar
 * -----------------------------------------------------------------------------
 */
.tz-sidebar {
  aside.widget {
    margin-bottom: 35px;
    ul {
      margin:0;
      padding:0;
      li {
        font-weight: 600;
        color: @color-text-h2;
        list-style: none;
        a {
          text-decoration: none;
          display: block;
          border-bottom: 1px solid #dddddd;
          color:@color-text-h2;
          list-style: none;
          text-transform: capitalize;
          padding:6px 0 5px;
          transition: all 0.3s ease-in-out 0s;
          -webkit-transition: all 0.3s ease-in-out 0s;
          -moz-transition: all 0.3s ease-in-out 0s;
          -ms-transition: all 0.3s ease-in-out 0s;
          -o-transition: all 0.3s ease-in-out 0s;
        }
      }
      li:hover {
        a {
          text-decoration: none;
          color: @color-theme;
        }
      }
    }
    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      -ms-appearance: none;
      -o-appearance: none;
      background: url("images/bg_down.png") no-repeat scroll right 20px top 19px rgba(0, 0, 0, 0);
      border:1px solid #dddddd;
      font-family: 'Raleway', serif;
      font-weight: 400;
      font-size: 15px;
      line-height: 24px;
      color:#797979;
      width:100%;
      padding:10px 15px;
    }
    &.widget_categories {
      ul {
        li {
          ul.children {
            li {
              font-weight: 400;
              a {
                color: #646569;
                border-bottom: none;
                padding-bottom: 0;
              }
              &:last-child {
                a {
                  border-bottom: 1px solid #dddddd;
                  padding-bottom: 5px;
                }
              }
            }
          }
        }
      }
    }
    &.widget_archive {
      ul {
        li {
          font-weight: 500;
          a {
            color: #646569;
          }
          &:hover {
            a {
              color: @color-theme;
            }
          }
        }
      }
    }
  }
  aside.widget:hover {
    .module-title:after {
      width: 100%;
    }
  }
  .footerattr {
    img {
      max-width: 100%;
      height: auto;
    }
  }
  aside.widget_nav_menu{
    ul.themeple_megemenu{
      >li{
        border:none;
        padding:0;
        >a{
          border-bottom: 1px solid #dddddd;
          padding:6px 0 5px;
          display: block;
          .transition(all 0.3s ease-in-out 0s);
        }
        >a:hover{
          background: none repeat scroll 0 0 #f6f6f6;
          text-indent: 10px;
        }
        ul{
          li{
            border:none;
            padding:0 0 0 20px;
            a{
              border-bottom: 1px solid #dddddd;
              padding:6px 0 5px;
              display: block;
              .transition(all 0.3s ease-in-out 0s);
            }
            a:hover{
              background: none repeat scroll 0 0 #f6f6f6;
              text-indent: 10px;
            }
          }
          li:hover{
            background:none;
            text-indent: 0;
          }
        }
      }
      >li:hover{
        background:none;
        text-indent: 0;
      }
    }
  }
  aside.widget_calendar {
    #calendar_wrap {
      border:1px solid #dddddd;
      table {
        width: 100%;
        caption {
          color: #333333;
          font-family: "Roboto Slab",serif;
          font-size: 16px;
          font-weight: 700;
          line-height: 24px;
          padding: 15px 10px;
        }
        thead {
          border-width: 1px 0 1px 0;
          border-style: solid;
          border-color: #dddddd;
          tr {
            th {
              text-align: center;
              color: #222222;
              font-family: "Roboto Slab",serif;
              font-size: 10px;
              font-weight: 400;
              padding:15px 9px;
            }
          }
        }
        tbody {
          border-width: 1px 0 1px 0;
          border-style: solid;
          border-color: #dddddd;
          tr {
            td {
              text-align: center;
              color: #333333;
              font-family: "Roboto Slab",serif;
              font-size: 13px;
              font-weight: 400;
              padding:8px;
            }
            td#today {
              background-color:@color-theme;
              color: #FFFFFF;
            }
          }
        }
        tfoot {
          tr {
            td {
              a {
                color: #666666;
                font-family: "Roboto Slab",serif;
                font-size: 13px;
                font-weight: 400;
                padding:6px;
                display: inline-block;
              }
            }
          }
        }
      }
    }
  }
  aside.widget_search {
    form {
      position: relative;
      label {
        display: none;
      }
      input.Tzsearchform {
        width: 100%;
        background: none;
        border:1px solid #dddddd;
        font-family: 'Raleway',sans-serif;
        font-size: 14px;
        font-weight: 400;
        font-style: italic;
        padding: 15px 50px 15px 19px;
        color: #111111;
      }
      input.searchsubmit {
        background-color: @color-theme;
        background-image: url(images/search.png)!important;
        background-position: center!important;
        background-repeat: no-repeat!important;
        border: none;
        font-size: 0;
        width:58px;
        height:52px;
        position: absolute;
        top:0;
        right:0;
      }
    }
  }
  aside.widget_flickr {
    .flickr-badge-wrapper {
      .flickr_badge_image {
        a {
          img {
            border:1px solid #dddddd;
            padding:4px;
            margin-right: 7px;
            margin-bottom: 7px;
            width: 58px;
            height: auto;
          }
        }
      }
    }
  }
  aside.widget_sp_image {
    .widget_sp_image-description {
      margin-top: 15px;
      p {
        color: #666666;
        font-family: "Raleway",serif;
        font-size: 14px;
        font-weight: 400;
        line-height: 24px;
      }
    }
  }
  aside.widget_rss {
    ul {
      li {
        border:none;
      }
      li:hover {
        border:none;
        background: none;
        text-indent: 0;
      }
    }
  }
  aside.widget_text {
    .textwidget {
      font-family: 'Raleway',sans-serif;
      font-size: 14px;
      font-weight: 300;
      line-height: 24px;
      color: #444444;
      img {
        max-width: 100%;
        height: auto;
      }
      .wp-caption {
        p.wp-caption-text {
          color: #444444;
          font-family: "Raleway",sans-serif;
          font-size: 12px;
          font-weight: 500;
          line-height: 24px;
        }
      }
    }
  }
  aside.widget_wpt {
    .wpt_widget_content {
      border:none;
      ul.wpt-tabs {
        li {
          width: auto;
          border: none;
          margin-right:1px;
          a {
            border: none;
            font-family: 'Roboto Slab',serif;
            font-size: 12px;
            text-transform: uppercase;
            color: #ffffff;
            background: #343434;
            line-height: 20px;
            padding:10px 14px;
            position: relative;
          }
          a:after {
            content: '';
            width: 100%;
            height: 4px;
            background: @color-theme;
            position: absolute;
            bottom: 0;
            left: 0;
          }
        }
        li:hover {
          text-indent: 0;
        }
        li.selected {
          a {
            background: @color-theme;
          }
        }
      }
      .inside {
        border:1px solid #dddddd;
        margin-top: -1px;
        padding:0;
        .tab-content {
          ul {
            li {
              padding:20px 10px;
              margin:0;
              .wpt-postmeta {
                font-family: 'Raleway',sans-serif;
                font-size: 13px;
                font-weight: 400;
                line-height: 13px;
                color: #777777;
              }
              .wpt_comment_content {
                p {
                  font-family: 'Raleway',sans-serif;
                  font-size: 13px;
                  font-weight: 300;
                  line-height: 24px;
                  color: #888888;
                }
              }
            }
            li:hover {
              text-indent: 0;
            }
          }
          .wpt-pagination {
            a:hover {
              text-decoration: none;
            }
            a {
              span {
                font-family: 'Roboto Slab',serif;
                font-size: 11px;
                font-weight: 400;
                text-transform: uppercase;
                line-height: 13px;
                color: #333333;
              }
            }
          }
        }
      }
    }
  }
  aside.dw_twitter {
    .dw-twitter-inner {
      .tweet-item {
        padding: 0 0 10px;
        margin:0 0 14px;
        border-bottom:1px solid #dce2ed;
        position: relative;
        &:after {
          position: absolute;
          content: "\f099";
          font-family: 'FontAwesome';
          font-size: 20px;
          background: @color-theme;
          color: #ffffff;
          top: 3px;
          left: 0;
          width: 27px;
          height: 27px;
          text-align: center;
          .border-radius(5px);
          line-height: 1.3em;
        }
        .tweet-content {
          font-family: @font-family-p;
          color: #646569;
          margin-left: 40px;
          a {
            color: @color-theme;
          }
          a:hover {
            text-decoration: none;
          }
          span {
            display: block;
            margin: 5px 0 5px;
          }
        }
      }
      .twitter-user {
        a.user {
          padding-top: 20px;
          display: block;
          font-family: 'Raleway',sans-serif;
          font-size: 14px;
          font-weight: 300;
          line-height: 24px;
          color: #444444;
        }
        a.user:hover {
          text-decoration: none;
        }
      }
    }
  }
  aside.widget_shopping_cart {
    .widget_shopping_cart_content {
      ul.cart_list {
        li {
          padding:10px 0 20px;
          margin:0;
          a {
            img {
              width: 70px;
              height: auto;
            }
          }
        }
        li:hover {
          text-indent: 0;
          background: none;
        }
        li:last-child {
          border: none;
        }
      }
      p.total {
        border-top: 1px solid #dddddd;
        padding:15px 0;
        margin: 0;
        strong {
          color: #444444;
          display: inline-block;
          font-family: "Roboto Slab",serif;
          font-size: 14px;
          font-weight: 400;
          line-height: 24px;
        }
        span {
          font-family: 'Raleway',sans-serif;
          font-size: 14px;
          font-weight: 500;
          line-height: 24px;
          color: #444444;
        }
      }
      p.buttons {
        a {
          display: inline-block;
          float: left;
          margin: 0 5px 5px 0;
          padding:15px 20px;
          font-family: 'Raleway',sans-serif;
          font-weight: 400;
          font-size: 12px!important;
          text-transform: uppercase;
          background: #343434;
          border-radius: 0;
          color: #FFFFFF;
          transition: all 0.3s ease-in-out 0s;
          -webkit-transition: all 0.3s ease-in-out 0s;
          -moz-transition: all 0.3s ease-in-out 0s;
          -ms-transition: all 0.3s ease-in-out 0s;
          -o-transition: all 0.3s ease-in-out 0s;
        }
        a:hover {
          text-decoration: none;
          background: @color-theme;
        }
      }
    }
  }
  aside.widget_price_filter {
    .price_slider_wrapper {
      .price_slider {
        background-color: #dddddd;
      }
      .price_slider_amount {
        margin-top:30px;
        button.button {
          display: inline-block;
          padding:15px 20px;
          font-family: 'Raleway',sans-serif;
          font-weight: 400;
          font-size: 12px!important;
          text-transform: uppercase;
          background: #343434;
          border-radius: 0;
          color: #FFFFFF;
          transition: all 0.3s ease-in-out 0s;
          -webkit-transition: all 0.3s ease-in-out 0s;
          -moz-transition: all 0.3s ease-in-out 0s;
          -ms-transition: all 0.3s ease-in-out 0s;
          -o-transition: all 0.3s ease-in-out 0s;
        }
        button.button:hover {
          background: @color-theme;
        }
        .price_label {
          font-family: 'Raleway',sans-serif;
          font-size: 14px;
          font-weight: 500;
          line-height: 24px;
          color: #444444;
          padding:8px 0;
        }
      }
    }
  }
  aside.widget_product_search {
    .woocommerce-product-search {
      position: relative;
      label {
        display: none;
      }
      input.search-field {
        width: 100%;
        background: none;
        border:1px solid #dddddd;
        font-family: 'Lato',sans-serif;
        font-weight: 400;
        font-style: italic;
        font-size: 14px;
        color: #aaaaaa;
        padding:11px 20px;
      }
      input[type="submit"] {
        background: url("images/icon-search.png") no-repeat;
        border:none;
        font-size: 0;
        width: 15px;
        height: 15px;
        padding: 0;
        position: absolute;
        top: 14px;
        right: 15px;
      }
    }
  }
  aside.woocommerce {
    .product_list_widget {
      li {
        padding:10px 0 10px;
        a:hover {
          text-indent: 0;
        }
        a {
          img {
            width: 70px;
            height: auto;
          }
        }
      }
    }
  }
}
.tz-sidebar aside.widget_text .textwidget p,.tz-sidebar aside.widget_text .textwidget p strong {
  color: #444444;
  font-family: @font-family-p;
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
}
.tz-sidebar aside.widget_wpt .wpt_widget_content .inside .tab-content ul li .wpt_avatar,.tz-sidebar aside.widget_wpt .wpt_widget_content .inside .tab-content ul li .wpt_thumbnail {
  padding:3px;
  margin:0 8px 0 0;
  border:1px solid #dddddd;
}
.tz-sidebar aside.widget_wpt .wpt_widget_content .inside .tab-content ul li .wpt_comment_meta,.tz-sidebar aside.widget_wpt .wpt_widget_content .inside .tab-content ul li .entry-title {
  line-height: 13px;
}
.tz-sidebar aside.widget_wpt .wpt_widget_content .inside .tab-content ul li .wpt_comment_meta span,.tz-sidebar aside.widget_wpt .wpt_widget_content .inside .tab-content ul li .entry-title a {
  font-family: @font-family-h3;
  font-size: 11px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 13px;
  color: #333333;
}
.tz-sidebar aside.widget_price_filter .price_slider_wrapper .price_slider .ui-slider-range,.tz-sidebar aside.widget_price_filter .price_slider_wrapper .price_slider span.ui-slider-handle {
  background-color:@color-theme;
}
aside.widget {
  margin-bottom: 35px;
  h3.module-title {
    position: relative;
    margin-bottom: 30px;
    span {
      font-family: @font-family-p;
      font-weight: 600;
      font-size: 20px;
    }
    &:before {
      content: '';
      height: 2px;
      width: 25px;
      position: absolute;
      left: 0;
      bottom: 0;
      background: @color-theme;
    }
  }
  form.searchform {
    position: relative;
    .tziconsearch,
    .tzsearchlabel,
    input.Tzsearchsubmit {
      display: none;
    }
    input.Tzsearchform {
      width: 100%;
      border-top: none;
      border-right: none;
      border-left: none;
      border-bottom: 1px solid #dae0ea;
      line-height: 30px;
    }
    .tz-icon-form-search {
      display: block;
      position: absolute;
      right: 0;
      top: 50%;
      color: #bec3cc;
      margin-top: -6px;
      transform: rotate(90deg);
      -moz-transform: rotate(90deg);
      -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
      -o-transform: rotate(90deg);
    }
  }
  &.tzsocial {
    a.tzSocial-no {
      color: #ffffff;
      padding: 5px 0;
      width: 29px;
      height: 29px;
      text-align: center;
      .border-radius (3px);
      background: @color-theme;
      text-decoration: none;
      display: inline-block;
      margin-bottom: 5px;
    }
  }
  .tagcloud {
    a {
      font-family: 'Lato', sans-serif;
      color: #c5c6c8;
      background: #50525b;
      padding: 5px 12px;
      .border-radius (3px);
      font-size: 14px !important;
      text-decoration: none;
      display: inline-block;
      margin-bottom: 5px;
      margin-right: 2px;
      transition: all 0.3s ease-in-out 0s;
      -webkit-transition: all 0.3s ease-in-out 0s;
      -moz-transition: all 0.3s ease-in-out 0s;
      -ms-transition: all 0.3s ease-in-out 0s;
      -o-transition: all 0.3s ease-in-out 0s;
      &:hover {
        background: @color-theme;
        color: #ffffff;
      }
    }
  }
  ul.pretty_flickr {
    &:after {
      display: table;
      clear: both;
      content: '';
    }
    li {
      float: left;
      margin-bottom: 10px;
      margin-right: 10px;
    }
    li {
      border: none;
      &:hover {
        background: none;
        text-indent: 0;
      }
    }
  }
  .tz-flickr {
    a {
      display: block;
      position: relative;
      border-bottom: none !important;
      padding: 0 !important;
      &:before {
        content: "\f002";
        font-family: 'FontAwesome';
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -14px;
        margin-left: -14px;
        z-index: 1;
        font-size: 11px;
        width: 28px;
        height: 28px;
        color: #ffffff;
        .border-radius (50%);
        background: rgba(36,39,50,0.6);
        padding: 2px 9px;
        opacity: 0;
        transition: all 0.4s ease-in-out 0s;
        -webkit-transition: all 0.4s ease-in-out 0s;
        -moz-transition: all 0.4s ease-in-out 0s;
        -ms-transition: all 0.4s ease-in-out 0s;
        -o-transition: all 0.4s ease-in-out 0s;
        transform: scale(0.5);
        -webkit-transform: scale(0.5);
        -moz-transform: scale(0.5);
        -ms-transform: scale(0.5);
        -o-transform: scale(0.5);
      }
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(228,89,20,0.9);
        opacity: 0;
        transition: all 0.4s ease-in-out 0s;
        -webkit-transition: all 0.4s ease-in-out 0s;
        -moz-transition: all 0.4s ease-in-out 0s;
        -ms-transition: all 0.4s ease-in-out 0s;
        -o-transition: all 0.4s ease-in-out 0s;
      }
      &:hover {
        &:before {
          opacity: 1;
          transform: scale(1);
          -webkit-transform: scale(1);
          -moz-transform: scale(1);
          -ms-transform: scale(1);
          -o-transform: scale(1);
        }
        &:after {
         opacity: 1;
        }
      }
      img {
        .border-radius (3px);
        float: none;
        margin: 0;
      }
    }
  }
  .multicolor-subscribe-form .mcolor-subbox {
    border: none !important;
    box-shadow: none;
    background: #f8f8f9;
    font-style: italic;
    color: #bfc8d3;
  }
  .multicolor-subscribe-form .mcolor-button {
    position: static;
    width: 100%;
    .border-radius ( 3px );
    font-weight: bold;
    text-transform: uppercase;
    margin-top: 10px;
  }
  &.tz_maniva_view_post {
    ul {
      li {
        border: none;
        position: relative;
        padding: 0;
        margin-bottom: 32px;
        &:before {
          clear: both;
          content: '';
          display: table;
        }
        &:after {
          position: absolute;
          content: '';
          height: 1px;
          width: 100%;
          left: 0;
          bottom: -15px;
          background: #dce2ed;
        }
        &:last-child:after {
          display: none;
        }
        &:hover {
          background: none;
          text-indent: 0;
        }
        h6 {
          margin: 0;
          a {
            color: @color-theme;
            font-weight: 400;
          }
        }
        .fearture-item {
          &:after {
            display: table;
            clear: both;
            content: '';
          }
        }
      }
    }
    .tz-fearture-img {
      a.tz-fearture-img-meetup {
        float: left;
        margin-top: 5px;
        position: relative;
        border: none;
        padding: 0;
        &:before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          transition: all 0.4s ease-in-out 0s;
          -moz-transition: all 0.4s ease-in-out 0s;
          -webkit-transition: all 0.4s ease-in-out 0s;
          -ms-transition: all 0.4s ease-in-out 0s;
          -o-transition: all 0.4s ease-in-out 0s;
        }
        &:hover:before {
          background: rgba(228, 89, 20, 0.9);
        }
        &:after {
          background: rgba(36, 39, 50, 0.6);
          .border-radius (50%);
          color: #ffffff;
          content: "\f002";
          font-family: "FontAwesome";
          font-size: 11px;
          height: 28px;
          left: 50%;
          margin-left: -14px;
          margin-top: -14px;
          opacity: 0;
          padding: 2px 9px;
          position: absolute;
          top: 50%;
          transform: scale(0.5);
          -moz-transform: scale(0.5);
          -webkit-transform: scale(0.5);
          -ms-transform: scale(0.5);
          -o-transform: scale(0.5);
          transition: all 0.4s ease-in-out 0s;
          -moz-transition: all 0.4s ease-in-out 0s;
          -webkit-transition: all 0.4s ease-in-out 0s;
          -ms-transition: all 0.4s ease-in-out 0s;
          -o-transition: all 0.4s ease-in-out 0s;
          width: 28px;
          z-index: 1;
        }
        &:hover:after {
          opacity: 1;
          transform: scale(1);
          -moz-transform: scale(1);
          -webkit-transform: scale(1);
          -ms-transform: scale(1);
          -o-transform: scale(1);
        }
      }
      img {
        width: 70px;
        height: 70px;
        .border-radius (3px);
      }
    }
    .tz-view-post-detail {
      margin: 0 0 0 85px;
      p {
        font-weight: 400;
        color: #646569;
      }
      .tz-view-post-date {
        span {
          color: @color-theme;
          font-weight: 400;
          display: inline-block;
        }
      }
    }
  }
}
