/* 7.23 Contact Meetup*/
.TzContactInfo {
  text-align: center;
  padding: 95px 0 110px 0;
  p {
    font-size: 18px;
    color: #aaaaaa;
    margin-bottom: 22px;
    line-height: 1.7em;
    &.tzContactPhone {
      font-size: 20px;
      line-height: 1.3em;
      margin: 0;
    }
    a {
      color: @color-theme;
      text-decoration: none;
    }
  }
  h4 {
    font-size: 22px;
    color: #dddddd;
    margin-bottom: 10px;
  }
}
.tz_map_meetup {
  position: relative;
  overflow: hidden;
  &:after {
    background: rgba(0,0,0,0.5);
    content: '';
    position: absolute;
    top: 0;
    left: -20px;
    right:0;
    height: 100%;
  }
  .tz_map_meetup_bk {
    position: absolute;
    top: 0;
    left: -20px;
    right: 0;
    height: 100%;
    background: rgba(9,9,31,0.4);
  }
  iframe {
    width: 100%;
    height: 100%;
    border: none;
  }
  .tz_meet_gmap_contact {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    a {
      border: 2px solid #ffffff;
      border-radius: 25px;
      color: #ffffff;
      display: inline-block;
      font-size: 12px;
      font-weight: 600;
      letter-spacing: 3px;
      padding: 13px 35px;
      text-decoration: none;
      text-transform: uppercase;
      transition: all 0.2s linear 0s;
      -moz-transition: all 0.2s linear 0s;
      -webkit-transition: all 0.2s linear 0s;
      -ms-transition: all 0.2s linear 0s;
      -o-transition: all 0.2s linear 0s;
      &:hover {
        color: #000000;
        background: #ffffff;
      }
    }
  }
}

/* Contact Meetup2 */
.tz_contact_meet_2 {
  margin-bottom: 27px;
}
.tz_contact_detail_meet {
  &:after {
    display: table;
    clear: both;
    content: '';
  }
  span.tz_contact_meet_icon {
    background: #3b86cf;
    width: 48px;
    height: 48px;
    .border-radius ( 50% );
    display: inline-block;
    float: left;
    text-align: center;
    i.fa {
      color: #ffffff;
      font-size: 24px;
      line-height: 48px;
    }
  }
  h4 {
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 700;
    margin-left: 65px;
    letter-spacing: 2.5px;
  }
}
.tz_meetup_contact_detail {
  padding-left: 65px;
  margin-top: -18px;
  p {
    margin-bottom: 5px;
    &:last-child {
      margin: 0;
    }
  }
}