/**
 * 5.0 - Single blog
 * -----------------------------------------------------------------------------
 */
.tz-blogSingle {
  padding:100px 0;
  .tz-blogSingleContent {
    padding-bottom: 50px;
    margin-bottom: 50px;
    .tz-SingleContentBox {
      padding: 40px 40px 74px;
      background: #f8f8f9;
      .border-radius(5px);
      overflow: hidden;
      .tz_SingleContentBox_general {
        position: relative;
        margin-bottom: 85px;
        .tz-blogAudio {
          iframe.iframe-full-audio {
            border: none;
          }
        }
        .tz-blogVideo {
          iframe.iframe-full {
            border: none;
          }
        }
        .tz_SingleContentBox_author {
          position: absolute;
          bottom: -58px;
          left: 30px;
          &.tz_SingleContentBox_author_hide {
            bottom: -25px;
            left: 0;
          }
          .author-avata {
            display: inline-block;
            position: relative;
            img {
              .border-radius(50%);
              border: 6px solid rgba(255,255,255,0.5);
            }
          }
          span.author-social {
            display: inline-block;
            margin-left: 8px;
            a.TzSocialLink {
              text-align: center;
              .border-radius(50%);
              color: #ffffff;
              font-size: 18px;
              display: inline-block;
              margin-right: 5px;
              border: 5px solid rgba(255,255,255,0.3);
              span {
                width: 40px;
                height: 40px;
                .border-radius(50%);
                line-height: 40px;
                display: inline-block;
                &.TzSocialLink_fa{
                  background: #4396e6;
                }
                &.TzSocialLink_twitter{
                  background: #15c0cc;
                }
                &.TzSocialLink_linkedin{
                  background: #4c5866;
                }
                &.TzSocialLink_dribbble{
                  background: #4c5866;
                }
                &.TzSocialLink_gp{
                  background: #f56048;
                }
              }
            }
          }
        }
        .tz_SingleContentBox_like {
          position: absolute;
          bottom: -45px;
          left: 0;
          width: 100%;
          text-align: right;
          .entry-single-comment {
            color: #95979d;
            border: 1px solid #dce2ed;
            .border-radius(25px);
            padding: 5px 15px;
            display: inline-block;
            font-family: @font-family-lato;
            a {
              display: block;
              text-decoration: none;
              color: #95979d;
            }
          }
          .share-wrap {
            display: inline-block;
            p {
              color: #95979d;
              border: 1px solid #dce2ed;
              .border-radius(25px);
              padding: 5px 15px;
              display: inline-block;
              font-family: @font-family-lato;
              cursor: pointer;
            }
            .share-wrap-content {
              position: absolute;
              top: 120%;
              right: 0;
              width: 100%;
              transform: scale(0);
              -moz-transform: scale(0);
              -webkit-transform: scale(0);
              -ms-transform: scale(0);
              -o-transform: scale(0);
              transform-origin: right center;
              -moz-transform-origin: right center;
              -webkit-transform-origin: right center;
              -ms-transform-origin: right center;
              -o-transform-origin: right center;
              transition: all 0.3s linear 0s;
              -moz-transition: all 0.3s linear 0s;
              -webkit-transition: all 0.3s linear 0s;
              -ms-transition: all 0.3s linear 0s;
              -o-transition: all 0.3s linear 0s;
              a {
                color: #ffffff;
                text-align: center;
                border-radius: 50%;
                display: inline-block;
                height: 40px;
                line-height: 40px;
                width: 40px;
                &.tz_social_facebook {
                  background: #4396e6;
                }
                &.tz_social_twitter {
                  background: #15c0cc;
                }
                &.tz_social_gplus {
                  background: #4c5866;
                }
                &.tz_social_pinterest {
                  background: #f56048;
                }
              }
            }
            .share-wrap-full {
              transform: scale(1);
              -moz-transform: scale(1);
              -webkit-transform: scale(1);
              -ms-transform: scale(1);
              -o-transform: scale(1);
            }
          }
        }
      }
      .tz-blogSlider {
        position: relative;
        ol.flex-control-nav{
          bottom: 0;
          text-align: right;
          li {
            margin:0 3px;
            &:last-child {
              margin-right: 20px;
            }
            a {
              background: #fff;
              .border-radius (50%);
              box-shadow: none;
              height: 12px;
              width: 12px;
              text-indent: 100em;
              &.flex-active{
                background: @color-theme;
              }
            }
          }
        }
      }
      h4.tzSingleBlog_title {
        font-size: 30px;
        margin: 0 0 3px;
        padding:0;
        font-weight: 600;
      }
      .tzinfomation {
        display: block;
        margin-bottom: 18px;
      }
      .single-content {
        margin-bottom: 52px;
        p {
          color:#646569;
          line-height: 1.8em;
          margin-bottom: 25px;
          &.tz_blockquote_meetup {
            margin: -19px 0 27px;
          }
        }
        blockquote {
          background-image: url("images/quote-blog.png");
          background-repeat: no-repeat;
          background-position: center center;
          border-left-color: #4396e6;
          font-size: 16px;
          padding: 0 0 0 20px;
          margin: 0;
          p {
            font-weight: 500;
            color: #95979d;
          }
        }
        .single-quote {
          background-image: url("images/quote_3.png");
          background-position: right center;
          background-repeat: no-repeat;
          p {
            font-weight: 600;
            color: @color-text-h2;
            font-size: 24px;
            line-height: 1.5em;
            padding-right: 44px;
            margin-bottom: 11px;
          }
        }
      }
      span.tztag {
        display: block;
        font-size: 14px;
        font-weight: 700;
        color: @color-text-h2;
        a {
          font-family: @font-family-lato;
          display: inline-block;
          color:#95979d;
          padding: 5px 20px;
          margin-left: 5px;
          margin-bottom:8px;
          .border-radius(25px);
          border: 1px solid #dce2ed;
          font-weight: 400;
          text-decoration: none;
        }
        a:first-child {
          margin-left: 38px;
        }
        a:hover {
          color: #ffffff;
          background-color: @color-theme;
          border-color: @color-theme;
        }
      }
    }
  }
  .author {
    padding-bottom: 50px;
    margin-bottom: 45px;
    background: #f8f8f9;
    .border-radius(5px);
    overflow: hidden;
    border-bottom: 5px solid rgba(0,0,0,0.02);
    .author-box {
      padding: 30px 55px 26px 25px;
      .author-avata {
        float: left;
        position: relative;
        .tz_author_avata {
          .border-radius(50%);
          border: 3px solid #f56048;
          overflow: hidden;
          width: 185px;
          height: 185px;
          span.tz_author_avata_img {
            .border-radius(50%);
            border: 5px solid #ffffff;
            overflow: hidden;
            display: block;
          }
        }
        .tz-author-social {
          height: 100%;
          left: 0;
          position: absolute;
          top: 0;
          width: 100%;
          a {
            display: inline-block;
            text-decoration: none;
            text-align: center;
            .border-radius(50%);
            overflow: hidden;
            position: absolute;
            span {
              display: block;
              width: 30px;
              height: 30px;
              line-height: 30px;
              .border-radius(50%);
              &.TzSocialLink-fa-facebook {
                background: #4396e6;
              }
              &.TzSocialLink-fa-twitter {
                background: #15c0cc;
              }
              &.TzSocialLink-fa-instagram {
                background: #4c5866;
              }
              &.TzSocialLink-fa-google-plus {
                background: #f56048;
              }
              &.TzSocialLink-fa-linkedin {
                background: @color-theme;
              }
              i.fa {
                font-size: 14px;
                color: #ffffff;
              }
            }
            &.tz_blog_meetup_social_even_0 {
              bottom: -40px;
              left: 32%;
            }
            &.tz_blog_meetup_social_even_1 {
              bottom: -40px;
              right: 32%;
            }
            &.tz_blog_meetup_social_even_2 {
              bottom: -30px;
              left: 13%;
            }
            &.tz_blog_meetup_social_even_3 {
              bottom: -30px;
              right: 13%;
            }
            &.tz_blog_meetup_social_odd_0 {
              bottom: -40px;
              left: 50%;
              margin-left: -20px;
            }
            &.tz_blog_meetup_social_odd_1 {
              bottom: -35px;
              left: 20%;
            }
            &.tz_blog_meetup_social_odd_2 {
              bottom: -35px;
              right: 25%;
            }
            &.tz_blog_meetup_social_odd_3 {
              bottom: -20px;
              left: 5px;
            }
            &.tz_blog_meetup_social_odd_4 {
              bottom: -20px;
              right: 12px;
            }
          }
        }
      }
      .author-info {
        margin:0 0 0 225px;
        h3 {
          margin:0;
          padding:0;
          a {
            font-family: 'Crete Round', serif;
            font-size: 24px;
            font-weight: 400;
            color: #3a424a;
            display: inline-block;
            padding:13px 0 6px;
            text-decoration: none;
          }
          span.tz_author_meetup {
            font-family: @font-family-lato;
            color: @color-theme;
            font-size: 11px;
            text-transform: uppercase;
          }
        }
        p {
          color: #646569;
          margin: 5px 0 0;
          line-height: 1.7em;
        }
      }
    }
    .author-box:after {
      content: '';
      display: block;
      clear: both;
    }
    .author-box:hover {
      .author-info {
        h3 {
          a:after {
            width: 100%;
          }
        }
      }
    }
  }
  .tzpost-pagenavi {
    position: relative;
    margin: 30px 0 0;
    a {
      display: inline-block;
      color: #22262b;
      text-decoration: none;
      transition: all 0.3s ease 0s;
      -moz-transition: all 0.3s ease 0s;
      -webkit-transition: all 0.3s ease 0s;
      -ms-transition: all 0.3s ease 0s;
      -o-transition: all 0.3s ease 0s;
      i {
        font-size: 10px;
        padding: 8px 0;
      }
      span {
        font-family: @font-family-p;
        font-size: 11px;
        font-weight: 600;
        margin: 0 6px;
        text-transform: uppercase;
      }
    }
    a[rel="prev"] {
      float: left;
    }
    a[rel="next"] {
      float:right;
    }
  }
  .tzpost-pagenavi:after {
    content: '';
    display: block;
    clear: both;
  }
  .tz_meetup_related_posts {
    h4 {
      font-weight: 600;
      color: #3a424a;
      font-size: 30px;
      margin-bottom: 31px;
    }
    .tz_meetup_related_posts_content {
      .tz-blog-item {
        overflow: hidden;
        .border-radius(5px);
        margin-bottom: 30px;
        &:after {
          display: table;
          clear: both;
          content: '';
        }
        .tz-blog-thubnail-item {
          width: 50%;
          float: left;
          height: 250px;
          position: relative;
          img {
            position: relative;
            max-width: none;
          }
          .tz-blogSlider-related {
            img {
              position: static;
            }
            .flex-control-nav {
              bottom: 0;
              z-index: 2;
            }
            .flex-control-paging {
              li {
                margin: 0 2px;
                a {
                  background: #ffffff;
                  box-shadow: none;
                  &:hover {
                    background: #4396e6;
                  }
                  &.flex-active {
                    background: #4396e6;
                  }
                }
              }
            }
          }
          .tz-blogVideo-related {
            position: relative;
            z-index: 1;
            .iframe-full {
              height: 250px;
              border: none;
            }
          }
          .tz-blogAudio-related {
            position: relative;
            z-index: 1;
            .iframe-full-audio {
              height: 250px;
              border: none;
            }
          }
          .tz-blog-icon-item-img {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 1;
            a {
              width: 30px;
              height: 30px;
              .border-radius(50%);
              line-height: 30px;
              background: #ffffff;
              color: #3a424a;
              display: inline-block;
              margin: 0 2px;
              cursor: pointer;
              font-size: 13px;
              transform: scale(0.5);
              -webkit-transform: scale(0.5);
              -moz-transform: scale(0.5);
              -ms-transform: scale(0.5);
              -o-transform: scale(0.5);
              opacity: 0;
              transition: all 0.4s ease 0s;
              -webkit-transition: all 0.4s ease 0s;
              -moz-transition: all 0.4s ease 0s;
              -ms-transition: all 0.4s ease 0s;
              -o-transition: all 0.4s ease 0s;
              .fa-link {
                padding-left: 2px;
                transform: rotate(90deg);
                -webkit-transform: rotate(90deg);
                -moz-transform: rotate(90deg);
                -ms-transform: rotate(90deg);
                -o-transform: rotate(90deg);
              }
              &:hover {
                background: @color-theme;
                color: #ffffff;
              }
            }
          }
        }
        .tz-blog-thubnail-item-img {
          &:after {
            position: absolute;
            width: 100%;
            height: 100%;
            content: '';
            top: 0;
            left: 0;
            background: rgba(0,0,0,0.2);
            transition: all 0.4s ease 0s;
            -webkit-transition: all 0.4s ease 0s;
            -moz-transition: all 0.4s ease 0s;
            -ms-transition: all 0.4s ease 0s;
            -o-transition: all 0.4s ease 0s;
          }
        }
        .tz-blog-thubnail-item-content {
          background: #f8f8f9;
          padding: 15px 15px 15px 20px;
          transition: all 0.4s ease 0s;
          -webkit-transition: all 0.4s ease 0s;
          -moz-transition: all 0.4s ease 0s;
          -ms-transition: all 0.4s ease 0s;
          -o-transition: all 0.4s ease 0s;
          &:after {
            border-bottom: 35px solid #f8f8f8;
            border-left: 20px solid transparent;
            border-top: 10px solid transparent;
            content: "";
            left: -12px;
            position: absolute;
            top: -5px;
            z-index: 1;
            transition: all 0.4s ease 0s;
            -webkit-transition: all 0.4s ease 0s;
            -moz-transition: all 0.4s ease 0s;
            -ms-transition: all 0.4s ease 0s;
            -o-transition: all 0.4s ease 0s;
          }
          h4 {
            font-weight: 600;
            font-size: 16px;
            line-height: 1.3em;
            margin-bottom: 5px;
            a {
              color: #3a424a;
              text-decoration: none;
              transition: all 0.4s ease 0s;
              -webkit-transition: all 0.4s ease 0s;
              -moz-transition: all 0.4s ease 0s;
              -ms-transition: all 0.4s ease 0s;
              -o-transition: all 0.4s ease 0s;
            }
          }
          span.entry-blog-meta {
            color: #cccccc;
            font-family: @font-family-lato;
            display: inline-block;
            transition: all 0.4s ease 0s;
            -webkit-transition: all 0.4s ease 0s;
            -moz-transition: all 0.4s ease 0s;
            -ms-transition: all 0.4s ease 0s;
            -o-transition: all 0.4s ease 0s;
            small.tzinfomation_time {
              font-size: 13px;
              display: inline-block;
              margin-right: 2px;
              &.tzinfomation_time_last {
                margin-right: 0;
              }
            }
            a {
              text-decoration: none;
              color: #cccccc;
              font-style: italic;
              display: inline-block;
              transition: all 0.4s ease 0s;
              -webkit-transition: all 0.4s ease 0s;
              -moz-transition: all 0.4s ease 0s;
              -ms-transition: all 0.4s ease 0s;
              -o-transition: all 0.4s ease 0s;
            }
          }
          p {
            line-height: 1.6em;
            color: #818d9a;
            transition: all 0.4s ease 0s;
            -webkit-transition: all 0.4s ease 0s;
            -moz-transition: all 0.4s ease 0s;
            -ms-transition: all 0.4s ease 0s;
            -o-transition: all 0.4s ease 0s;
          }
          small.tz_meetup_single_comment {
            font-family: @font-family-lato;
            font-size: 13px;
            display: inline-block;
            transition: all 0.4s ease 0s;
            -webkit-transition: all 0.4s ease 0s;
            -moz-transition: all 0.4s ease 0s;
            -ms-transition: all 0.4s ease 0s;
            -o-transition: all 0.4s ease 0s;
            a {
              color: #9fadbd;
              text-decoration: none;
            }
            i.fa {
              margin-left: 2px;
              font-size: 11px;
              margin-top: 1px;
            }
          }
        }
        &:hover {
          .tz-blog-icon-item-img {
            a {
              opacity: 1;
              transform: scale(1);
              -webkit-transform: scale(1);
              -moz-transform: scale(1);
              -ms-transform: scale(1);
              -o-transform: scale(1);
            }
          }
          .tz-blog-thubnail-item-img {
            &:after {
              background: rgba(0,0,0,0.5);
            }
          }
          .tz-blog-thubnail-item-content {
            background: @color-theme;
            &:after {
              border-bottom-color: @color-theme;
            }
            h4 {
              a {
                color: #ffffff;
              }
            }
            span.entry-blog-meta {
              color: #e9e4e4;
              a {
                color: #e9e4e4;
              }
            }
            p {
              color: #f4d4cf;
            }
            small.tz_meetup_single_comment {
              color: #e9e4e4;
            }
          }
        }
      }
    }
  }
}
.tz-blogSingle .tz-blogSingleContent .tz-SingleContentBox .tz-blogVideo,
.tz-blogSingle .tz-blogSingleContent .tz-SingleContentBox .tz-BlogAudio,
.tz-blogSingle .tz-blogSingleContent .tz-SingleContentBox .tz-blogSlider,
.tz-blogSingle .tz-blogSingleContent .tz-SingleContentBox .tz-BlogImage {
  position: relative;
  //margin-bottom: 85px;
  .border-radius(5px);
}
.tz-blogSingle .tz-blogSingleContent .tz-SingleContentBox .tz-blogSlider ul.slides li img,
.tz-blogSingle .tz-blogSingleContent .tz-SingleContentBox .tz-BlogImage img {
  max-width:100%;
  height: auto;
  display: block;
  margin: 0 auto;
}
.tz-blogSingle .tz-blogSingleContent .tz-SingleContentBox .tzinfomation small,
.tz-blogSingle .tz-blogSingleContent .tz-SingleContentBox .tzinfomation a {
  font-family: @font-family-lato;
  font-style: italic;
  color:#95979d;
  margin-right: 10px;
}
.tz-blogSingle .tz-blogSingleContent .tz-SingleContentBox .tzinfomation small.tzinfomation_time {
  margin-right: 3px;
  font-style: normal;
}
.tz-blogSingle .tz-blogSingleContent .tz-SingleContentBox .tzinfomation small.tzinfomation_time.tzinfomation_time_last,
.tz-blogSingle .tz-blogSingleContent .tz-SingleContentBox .tzinfomation a i,
.tz-blogSingle .tz-blogSingleContent .tz-SingleContentBox .tzinfomation span.tzcategory i{
  margin-right: 10px;
}