/* 2.2 Header Home */
.tz-headerHome {
  z-index: 9999999;
  a.tz_logo {
    padding:35px 40px 35px 0;
    text-decoration: none;
    transition: all 0.2s linear 0s;
    -moz-transition: all 0.2s linear 0s;
    -webkit-transition: all 0.2s linear 0s;
    -ms-transition: all 0.2s linear 0s;
    -o-transition: all 0.2s linear 0s;
    p {
      font-family: @font-family-h3;
      color: #fff;
      line-height: 1em;
      font-size: 30px;
      margin: 0;
      transition: all 0.2s linear 0s;
      -moz-transition: all 0.2s linear 0s;
      -webkit-transition: all 0.2s linear 0s;
      -ms-transition: all 0.2s linear 0s;
      -o-transition: all 0.2s linear 0s;
    }
  }
  .navbar-toggle {
    background: @color-theme;
  }
  nav {
    ul.tz-nav {
      padding:0;
      & > li {
        & > a {
          margin: 40px 0;
          font-size: 12px;
          font-weight: 600;
          color: @color-text-h2;
          line-height: 1.4em;
          display: block;
          padding: 0 14px;
          text-transform: uppercase;
          transition: all 0.2s linear 0s;
          -moz-transition: all 0.2s linear 0s;
          -webkit-transition: all 0.2s linear 0s;
          -ms-transition: all 0.2s linear 0s;
          -o-transition: all 0.2s linear 0s;
        }
        & > a:hover {
          background: none;
          color:@color-theme;
        }
      }
      & > li:last-child {
        & > a {
          border: none;
          box-shadow: none;
          padding-right: 0;
        }
      }
    }
  }
  .tz-headerRight {
   text-align: right;
    ul {
      margin:0;
      padding:0;
      li {
        list-style: none;
        display: inline-block;
        a {
          display: inline-block;
          padding: 0 12px;
          i {
            font-size: 14px;
            color:#ffffff;
          }
        }
        &:first-child {
          a {
            padding-left: 0;
          }
        }
        &:last-child {
          a {
            padding-right: 0;
          }
        }
      }
    }
    ul:after {
      display: block;
      content: "";
      clear: both;
    }
  }
  .tz-headerSlider {
    position: relative;
    .tz-menuVertical {
      position: absolute;
      right: 120px;
      top: 50%;
      z-index: 999;
      ul {
        margin:0;
        padding:0;
        max-height:none;
        li {
          position: relative;
          float: none;
          margin-bottom: 39px;
          list-style: none;
          a {
            display: block;
            background: none;
            margin:0;
            padding:8px 0 8px 0;
            font-family: "Roboto Slab", serif;
            font-size: 20px;
            font-weight: 400;
            line-height: 24px;
            color:#fff;
            text-align: right;
            transition: all 0.3s ease-in-out 0s;
            -moz-transition: all 0.3s ease-in-out 0s;
            -webkit-transition: all 0.3s ease-in-out 0s;
            -ms-transition: all 0.3s ease-in-out 0s;
            -o-transition: all 0.3s ease-in-out 0s;
            span {
              padding-left: 87px;
              transition: all 0.3s ease-in-out 0s;
              -moz-transition: all 0.3s ease-in-out 0s;
              -webkit-transition: all 0.3s ease-in-out 0s;
              -ms-transition: all 0.3s ease-in-out 0s;
              -o-transition: all 0.3s ease-in-out 0s;
            }
            span.tzMenuShapes {
              margin:0;
              padding:0;
              background: #ffffff;
              display: block;
              width: 20px;
              height: 20px;
              position: absolute;
              top: 11px;
              right: 53px;
              transform:rotate(45deg);
              -moz-transform:rotate(45deg);
              -webkit-transform:rotate(45deg);
              -ms-transform:rotate(45deg);
              -o-transform:rotate(45deg);
              transition: all 0.3s ease-in-out 0s;
              -moz-transition: all 0.3s ease-in-out 0s;
              -webkit-transition: all 0.3s ease-in-out 0s;
              -ms-transition: all 0.3s ease-in-out 0s;
              -o-transition: all 0.3s ease-in-out 0s;
            }
            span.tzMenuShapes:after {
              content:'';
              width:100%;
              height:100%;
              transform:scale(1.45);
              -webkit-transform:scale(1.45);
              -moz-transform:scale(1.45);
              -ms-transform:scale(1.45);
              -o-transform:scale(1.45);
              border:1px solid #fff;
              position: absolute;
              top: 0;
              left: 0;
            }
          }
          a:after {
            content:'';
            width:100%;
            height: 40px;
            border-right:2px solid #d0d0d0;
            position: absolute;
            top:100%;
            right: 62px;
          }
        }
        li:last-child {
          margin-bottom:0;
          a:after {
            content:none;
          }
        }
      }
    }
    .tz-menuVertical:after {
      content:'';
      display:block;
      clear:both;
    }
    .tz-menuButton {
      position: absolute;
      width: 100%;
      bottom: 0;
      left: 0;
      span {
        color:#fff;
        display: block;
        margin-bottom: 10px;
        font-family: "Roboto Slab", serif;
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        text-transform: uppercase;
      }
      a {
        display: inline-block;
        width: 40px;
        height: 46px;
        background: url(images/down.png) no-repeat;
        background-position: center 0;
      }
    }
  }
}
.tz-homeType1 {
  position: fixed;
  width: auto;
  left: 0;
  right: 0;
  top: 0;
  background: none repeat scroll 0 0 rgba(0, 0, 0, 0);
  transition: all 0.3s linear 0s;
  -moz-transition: all 0.3s linear 0s;
  -webkit-transition: all 0.3s linear 0s;
  -ms-transition: all 0.3s linear 0s;
  -o-transition: all 0.3s linear 0s;
  &.tz-menuEffect {
    background: rgba(0, 0, 0, 0.7);
  }
  .tz-header-content {
    position: relative;
  }
  .tz_meetup_header_option {
    background: @color-theme;
    padding: 12px 0;
    transition: all 0.2s linear 0s;
    -moz-transition: all 0.2s linear 0s;
    -webkit-transition: all 0.2s linear 0s;
    -ms-transition: all 0.2s linear 0s;
    -o-transition: all 0.2s linear 0s;
    .tz_meetup_header_option_phone {
      span {
        display: inline-block;
        color: #ffffff;
        img {
          margin-right: 5px;
        }
        a {
          color: #ffffff;
          text-decoration: none;
        }
        &:first-child {
          margin-right: 40px;
        }
      }
    }
  }
  &.tz-menuEffect-1 {
    .tz_meetup_header_option {
      height: 0;
      padding: 0;
      overflow: hidden;
    }
  }
  a.tz_logo {
    padding: 42px 40px 40px 0;
    .logo_lager_2 {
      display: none;
    }
  }
  nav {
    ul.tz-nav {
      > li > a {
        margin: 0;
        padding: 45px 0;
        position: relative;
        line-height: 1.6em;
        color: #ffffff;
        &:before {
          position: absolute;
          background: @color-theme;
          bottom: 40px;
          left: 0;
          content: '';
          height: 3px;
          width: 0;
          transition: all 0.2s linear 0s;
          -moz-transition: all 0.2s linear 0s;
          -webkit-transition: all 0.2s linear 0s;
          -ms-transition: all 0.2s linear 0s;
          -o-transition: all 0.2s linear 0s;
        }
        &:hover {
          color: #ffffff;
          &:before {
            width: 100%;
          }
        }
      }
    }
  }
  &.tz-homeTypeRelative {
    position: relative;
    background: transparent;
    nav {
      ul.tz-nav {
        > li > a {
          color: @color-text-h2;
        }
      }
    }
    button.tz-search i.fa {
      color: @color-text-h2;
    }
  }
  .container {
    .tzHeaderContainer:after {
      content:'';
      clear: both;
      display: block;
    }
    .tzHeaderContainer {
      .tz-headerRight {
        ul {
          li {
            a {
              border: none;
              padding: 0;
              height: auto;
              width: auto;
              &:hover {
                background: transparent;
              }
              i:hover {
                color: @color-theme;
              }
            }
          }
        }
      }
    }
  }
  &.tz-home-bk-type1 {
    background: #fff;
    a.tz_logo {
      p {
        color: @color-text-body;
      }
      .logo_lager_2 {
        display: block;
      }
      .logo_lager.logo_lager_none {
        display: none;
      }
      padding: 7px 40px 25px 0;
    }
    nav {
      ul.tz-nav {
        > li > a {
          color: @color-text-body;
          margin: 20px 0 35px;
          &:hover {
            color: @color-theme;
          }
        }
      }
    }
    .tz-headerRight {
      top: 13px;
      ul {
        li {
          a {
            i {
              color: @color-text-body;
            }
          }
        }
      }
    }
  }
  button.tz-search {
    background: none;
    border: none;
    margin: 45px 0 0 25px;
    padding: 0;
    display: block;
    transform: rotate(90deg);
    -moz-transform: rotate(90deg);
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    -o-transform: rotate(90deg);
    overflow: hidden;
    i.fa {
      color: #ffffff;
    }
  }
  .tz-form-search {
    background: #fff none repeat scroll 0 0;
    display: none;
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    z-index: 9999;
    .container {
      height: 100%;
    }
    form {
      height: 100%;
      margin: 0;
      padding: 0;
      position: relative;
      width: 100%;
    }
    .tziconsearch,
    label.tzsearchlabel,
    .Tzsearchsubmit {
      display: none;
    }
    .Tzsearchform {
      border: 0 none;
      color: #222;
      font-size: 20px;
      font-weight: 300;
      height: 100%;
      line-height: 30px;
      margin: 0;
      padding: 5px 0;
      width: 100%;
    }
    .fa.tz-icon-form-search {
      display: none;
    }
    .fa.tz-form-close {
      color: @color-theme;
      cursor: pointer;
      font-size: 18px;
      margin-top: -11px;
      position: absolute;
      right: 0;
      top: 50%;
      display: block;
    }
  }
}
.tz-homeType1.tzAdmin_bar {
  margin-top: 32px;
}
.tz-homeEff {
  background: none repeat scroll 0 0 rgba(0, 0, 0, 0.7)!important;
}
.tz-homeType2 {
  border-bottom: 1px solid rgba(255,255,255,0.1);
  position:fixed;
  width: auto;
  top:0;
  left: 0;
  right: 0;
  background: none repeat scroll 0 0 rgba(0, 0, 0, 0);
  transition: all 0.3s linear 0s;
  -moz-transition: all 0.3s linear 0s;
  -webkit-transition: all 0.3s linear 0s;
  -ms-transition: all 0.3s linear 0s;
  -o-transition: all 0.3s linear 0s;
  .tzHeaderContainer:after {
    content: '';
    clear: both;
    display: table;
  }
  nav {
    ul.tz-nav {
      > li {
        > a {
          margin: 0;
          padding: 40px 0;
          color: #ffffff;
          &:before {
            position: absolute;
            bottom: 35%;
            left: 0;
            content: '';
            background: @color-theme;
            height: 3px;
            width: 0;
            transition: all 0.3s linear 0s;
            -moz-transition: all 0.3s linear 0s;
            -webkit-transition: all 0.3s linear 0s;
            -ms-transition: all 0.3s linear 0s;
            -o-transition: all 0.3s linear 0s;
          }
          &:hover {
            color: #ffffff;
            &:before {
              width: 100%;
            }
          }
        }
        &.current {
          > a {
            &:before {
              width: 100%;
            }
          }
        }
      }
    }
  }
  &.tz-homeTypeRelative {
    position: relative;
    background: rgba(0,0,0,0.7);
  }
}
.tz-homeType2.tzAdmin_bar {
  margin-top: 32px;
}
.tz-homeType3 {
  position:fixed;
  width: 100%;
  top:0;
  left: 0;
  z-index: 99999999;
  background: none repeat scroll 0 0 rgba(0, 0, 0, 0);
  transition: all 0.3s linear 0s;
  -moz-transition: all 0.3s linear 0s;
  -webkit-transition: all 0.3s linear 0s;
  -ms-transition: all 0.3s linear 0s;
  -o-transition: all 0.3s linear 0s;
  a.tz_logo {
    padding:0 19px;
    margin: 35px 0 34px;
    line-height: 13px;
    border-left: 1px solid #494d54;
    border-right: 1px solid #494d54;
  }
  .tz-headerRight {
    position:absolute;
    right: 73px;
    top: 34px;
    ul {
      li {
        a {
          display: inline-block;
          border: none;
          margin:0;
          transform: none;
          text-align: center;
          i {
            font-size: 14px;
            color:#d0d0d0;
            transform: none;
          }
        }
        a:hover {
          background: none;
          i {
            color:@color-theme;
          }
        }
      }
    }
  }
  nav {
    ul.tz-nav {
      & > li {
        & > a {
          font-family: 'Roboto Condensed', sans-serif;
          border:none;
          box-shadow: none;
        }
      }
    }
    ul.tz-shopMenu {
      margin:0;
      padding:0;
      li {
        position: relative;
        list-style: none;
        float: left;
        form.searchform {
          z-index: 9999;
          position: absolute;
          top:50%;
          right: 35px;
          margin-top: -25px;
          input.Tzsearchform {
            width: 600px;
            display: none;
            height: 50px;
            border:1px solid #dddddd;
            padding:10px 15px;
            font-family: 'Roboto Condensed',sans-serif;
            font-size: 16px;
            font-weight: 400;
            color:#666666;
            background: #ffffff;
            transition: all 0.3s ease-in-out 0s;
            -webkit-transition: all 0.3s ease-in-out 0s;
            -moz-transition: all 0.3s ease-in-out 0s;
            -ms-transition: all 0.3s ease-in-out 0s;
            -o-transition: all 0.3s ease-in-out 0s;
          }
        }
        button.tz-icon-search {
          background: none;
          border:none;
          border-right: 1px solid #000000;
          box-shadow: 1px 0 0 0 #555555;
          color: #ffffff;
          display: block;
          font-family: 'Roboto Condensed', sans-serif;
          font-size: 10px;
          font-weight: 400;
          line-height: 10px;
          margin: 40px 0 32px;
          padding: 0 14px 0 0;
          text-transform: uppercase;
        }
        button.tz-icon-search:hover {
          color:@color-theme;
        }
        a {
          border-right: 1px solid #000000;
          box-shadow: 1px 0 0 0 #555555;
          color: #ffffff;
          display: block;
          font-family: 'Roboto Condensed', sans-serif;
          font-size: 10px;
          font-weight: 400;
          line-height: 10px;
          margin: 40px 0 32px;
          padding: 0 14px;
          text-transform: uppercase;
          position: relative;
        }
        a:hover {
          text-decoration: none;
          color:@color-theme;
        }
        a.tzShopMenu-Icon {
          span.amount {
            margin-left: 14px;
          }
          span.tzMenuShop-qty {
            display:inline-block;
            width: 14px;
            height:14px;
            background: @color-theme;
            border-radius: 50%;
            color: #ffffff;
            font-family: "Roboto Condensed",sans-serif;
            font-size: 10px;
            line-height: 15px;
            text-align: center;
            position:absolute;
            top:-5px;
            left:24px;
          }
        }
      }
    }
    ul.tz-shopMenu:after {
      content: '';
      display: block;
      clear: both;
    }
  }
}
.tz-headerHome .tz-headerSlider .tz-menuVertical ul li.current a,
.tz-headerHome .tz-headerSlider .tz-menuVertical ul li a:hover {
  color:@color-theme;
  text-decoration: none;
}
.tz-headerHome .tz-headerSlider .tz-menuVertical ul li.current a span.tzMenuShapes,
.tz-headerHome .tz-headerSlider .tz-menuVertical ul li a:hover span.tzMenuShapes {
  background: @color-theme;
}
.tz-revoslider {
  position: relative;
}
.tz-revoslider:before {
  position: absolute;
  content: '';
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.7);
  width: 100%;
  height: 100%;
  z-index: 1;
}
.tz_logo_bg_slider {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  height: 100%;
  width: 100%;
  .ds-table-cell {
    img {
      transition: all 0.1s linear 0s;
      -moz-transition: all 0.1s linear 0s;
      -webkit-transition: all 0.1s linear 0s;
      -ms-transition: all 0.1s linear 0s;
      -o-transition: all 0.1s linear 0s;
    }
  }
}
.tzAdmin_bar {
  .tz-menuEffect {
    margin-top:30px;
  }
}
