/* 7.13 Slider Home Meetup */

/* bxSlider */

.bx-wrapper {
  position: relative;
  background: rgba(0,0,0,0.7);
  padding: 0;
  *zoom: 1;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
}
.bx-wrapper img {
  max-width: none;
  display: block;
  position: absolute;
}
.bxslider {
  margin: 0;
  padding: 0;
  height: 100vh;
}
.bxslider li {
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  overflow: hidden;
  height: 100vh;
  video.videoID {
    width: auto;
    height: auto;
    max-height: 4000%;
    min-height: 100%;
    max-width: 1000%;
    min-width: 100%;
  }
}
ul.bxslider {
  list-style: none;
}
.bx-viewport {
  /*fix other elements on the page moving (on Chrome)*/
  -webkit-transform: translatez(0);
}
/** THEME
===================================*/

.bx-wrapper .bx-pager,
.bx-wrapper .bx-controls-auto {
  position: absolute;
  bottom: 40px;
  width: 100%;
  z-index: 55;
}
/* LOADER */
.bx-wrapper .bx-loading {
  min-height: 50px;
  background: url('images/bx_loader.gif') center center no-repeat #ffffff;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2000;
}
/* PAGER */
.bx-wrapper .bx-pager {
  text-align: center;
  font-size: .85em;
  font-family: Arial;
  font-weight: bold;
  color: #666;
}
.bx-wrapper .bx-pager.bx-default-pager a {
  text-indent: -9999px;
  border: 2px solid @color-theme;
  display: block;
  width: 15px;
  height: 15px;
  margin: 0 5px;
  outline: 0;
  .border-radius (50%);
}
.bx-wrapper .bx-pager.bx-default-pager a:hover,
.bx-wrapper .bx-pager.bx-default-pager a.active,
.bx-wrapper .bx-pager.bx-default-pager a:focus {
  background: @color-theme;
}
.bx-wrapper .bx-pager-item,
.bx-wrapper .bx-controls-auto .bx-controls-auto-item {
  display: inline-block;
  *zoom: 1;
  *display: inline;
}
.bx-wrapper .bx-pager-item {
  font-size: 0;
  line-height: 0;
}
/* DIRECTION CONTROLS (NEXT / PREV) */
.bx-wrapper .bx-prev {
  left: 10px;
  padding: 8px 11px;
  transition: all 0.2s linear 0s;
  -moz-transition: all 0.2s linear 0s;
  -webkit-transition: all 0.2s linear 0s;
  -ms-transition: all 0.2s linear 0s;
  -o-transition: all 0.2s linear 0s;
  &:hover {
    color: #ffffff;
    background: @color-theme;
    border-color: @color-theme;
  }
}
.bx-wrapper .bx-prev:hover,
.bx-wrapper .bx-prev:focus {
  background-position: 0 0;
}
.bx-wrapper .bx-next {
  right: 10px;
  padding: 8px 15px;
  transition: all 0.2s linear 0s;
  -moz-transition: all 0.2s linear 0s;
  -webkit-transition: all 0.2s linear 0s;
  -ms-transition: all 0.2s linear 0s;
  -o-transition: all 0.2s linear 0s;
  &:hover {
    color: #ffffff;
    background: @color-theme;
    border-color: @color-theme;
  }
}
.bx-wrapper .bx-next:hover,
.bx-wrapper .bx-next:focus {
  background-position: -43px 0;
}
.bx-wrapper .bx-controls-direction a {
  position: absolute;
  top: 50%;
  margin-top: -16px;
  outline: 0;
  z-index: 9999;
  font-size: 32px;
  color: #b0b2b6;
  width: 40px;
  height: 40px;
  border: 1px solid #b0b2b6;
  text-decoration: none;
  .border-radius (50%);
  &.bx-next:before {
    content: "\f105";
    font-family: 'FontAwesome';
  }
  &.bx-prev:before {
    content: "\f104";
    font-family: 'FontAwesome';
  }
}
.bx-wrapper .bx-controls-direction a.disabled {
  display: none;
}
/* AUTO CONTROLS (START / STOP) */
.bx-wrapper .bx-controls-auto {
  text-align: center;
}
.bx-wrapper .bx-controls-auto .bx-start {
  display: block;
  text-indent: -9999px;
  width: 10px;
  height: 11px;
  outline: 0;
  background: url('images/controls.png') -86px -11px no-repeat;
  margin: 0 3px;
}
.bx-wrapper .bx-controls-auto .bx-start:hover,
.bx-wrapper .bx-controls-auto .bx-start.active,
.bx-wrapper .bx-controls-auto .bx-start:focus {
  background-position: -86px 0;
}
.bx-wrapper .bx-controls-auto .bx-stop {
  display: block;
  text-indent: -9999px;
  width: 9px;
  height: 11px;
  outline: 0;
  background: url('images/controls.png') -86px -44px no-repeat;
  margin: 0 3px;
}
.bx-wrapper .bx-controls-auto .bx-stop:hover,
.bx-wrapper .bx-controls-auto .bx-stop.active,
.bx-wrapper .bx-controls-auto .bx-stop:focus {
  background-position: -86px -33px;
}
/* PAGER WITH AUTO-CONTROLS HYBRID LAYOUT */
.bx-wrapper .bx-controls.bx-has-controls-auto.bx-has-pager .bx-pager {
  text-align: left;
  width: 80%;
}
.bx-wrapper .bx-controls.bx-has-controls-auto.bx-has-pager .bx-controls-auto {
  right: 0;
  width: 35px;
}
/* IMAGE CAPTIONS */
.bx-wrapper .bx-caption {
  position: absolute;
  bottom: 0;
  left: 0;
  background: #666;
  background: rgba(80, 80, 80, 0.75);
  width: 100%;
}
.bx-wrapper .bx-caption span {
  color: #fff;
  font-family: Arial;
  display: block;
  font-size: .85em;
  padding: 10px;
}
.tz_home_slider_meetup {
  position: relative;
  .meetup_bl_slider_home {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.7);
    z-index: 50;
  }
  .tz_content_slider_meetup {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 52;
    top: 0;
    left: 0;
    .tz_meetup_social {
      display: inline-block;
      position: relative;
      margin-bottom: 15px;
      .meetup_line_left {
        background: #ffffff;
        height: 1px;
        left: 100%;
        position: absolute;
        top: 50%;
        width: 25px;
      }
      .meetup_line_right {
        background: #ffffff;
        height: 1px;
        right: 100%;
        position: absolute;
        top: 50%;
        width: 25px;
      }
    }
    a {
      text-decoration: none;
      color: #ffffff;
      margin: 0 10px;
      font-size: 18px;
    }
    .tz_meetup_slider_home_text {
      p {
        font-weight: 500;
        color: #fff;
      }
      h4 {
        font-size: 60px;
        font-weight: 200;
        color: #ffffff;
        line-height: 1.1em;
      }
    }
  }
}
.tz_meetup_countdown {
  .tz_meetup_countdown_time {
    display: inline-block;
    position: relative;
    margin: 0 25px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: -25px;
      width: 1px;
      height: 25px;
      margin-top: -12.5px;
      background: @color-theme;
    }
    &:last-child:after {
      display: none;
    }
    span {
      font-size: 65px;
      color: #ffffff;
      display: block;
      line-height: 1.2em;
      font-family: @font-family-h3;
      font-weight: 100;
    }
    b {
      display: block;
      font-size: 16px;
      text-transform: uppercase;
      color: #ffffff;
    }
  }
  span.tz_meetup_countdown_over {
    color: #ffffff;
    display: inline-block;
    font-size: 30px;
    line-height: 1.4em;
    margin: 20px 0 0;
    text-transform: uppercase;
  }
}
.tz_slider_meetup_btn {
  margin-top: 25px;
  ul {
    li {
      list-style: none;
      display: inline-block;
    }
  }
  a.tz_slider_meetup_btn_1 {
    text-decoration: none;
    color: #ffffff;
    font-weight: 700;
    background: @color-theme;
    padding: 12px 35px;
    display: inline-block;
    font-size: 12px;
    border: 2px solid @color-theme;
    margin: 0 10px;
    .border-radius (25px);
    letter-spacing: 3px;
    transition: all 0.2s linear 0s;
    -moz-transition: all 0.2s linear 0s;
    -webkit-transition: all 0.2s linear 0s;
    -ms-transition: all 0.2s linear 0s;
    -o-transition: all 0.2s linear 0s;
    &.tz_slider_meetup_btn_2 {
      background: transparent;
      border-color: #ffffff;
      &:hover {
        background: #ffffff;
        color: #000;
      }
    }
    &:hover {
     background: transparent;
    }
  }
}

