/* 2.4 Breadcrumb */
.tz-sectionBreadcrumb {
  background-color: #434758;
  background-image: url("images/breadcrumb_meetup.png");
  background-repeat: no-repeat;
  padding: 60px 0;
  background-size: cover;
  h1.tz-sectionBreadcrumb-month {
    color: #ffffff;
  }
  .tz_breadcrumb_single_cat_title {
    h4 {
      color: #ffffff;
      font-size: 18px;
      font-weight: 400;
      line-height: 1.7em;
      text-transform: uppercase;
    }
  }
  .tz-breadcrumb {
    text-align: right;
    &.tz-breadcrumb_archive {
      text-align: left;
    }
    h4 {
      font-size: 14px;
      line-height: 1.7em;
      text-transform: capitalize;
      color: #ffffff;
      span {
        color: #ffffff;
        font-weight: 400;
        a {
          color: #ffffff;
          text-decoration: none;
        }
      }
    }
  }
}
