/* 7.22 tz-ask-questions */
.tz_meetup_ask_question {
  margin-bottom: 75px;
}
.tz_meetup_question {
  &:after {
    display: table;
    content: '';
    clear: both;
  }
  span.tz_icon_meetup_question {
    display: inline-block;
    width: 20px;
    height: 20px;
    .border-radius(50%);
    background: @color-theme;
    text-align: center;
    float: left;
    margin-top: 4px;
    i.fa {
      color: #ffffff;
      font-size: 18px;
      line-height: 20px;
    }
  }
  h4 {
    margin-left: 40px;
    font-size: 18px;
    font-weight: 600;
  }
}
.tz_meetup_ask {
  padding-left: 40px;
  margin-top: 10px;
  p {
    font-weight: 300;
    line-height: 1.7em;
  }
}