/* 7.18 tz_meetup_breadcrumb */
.tz_meetup_breadcrumb {
  background-image: url("images/breadcrumb_meetup.png");
  background-repeat: no-repeat;
  background-color: #434758;
  padding: 60px 0;
  background-size: cover;
  .tz_meetup_breadcrumb_title {
    h4 {
      font-weight: 400;
      color: #ffffff;
      text-transform: uppercase;
      font-size: 18px;
      line-height: 1.7em;
    }
  }
  .tz_meetup_breadcrumb_content {
    text-align: right;
    h4 {
      font-size: 14px;
      line-height: 1.7em;
      span {
        color: #ffffff;
        font-weight: 400;
        a {
          text-decoration: none;
          color: #ffffff;
        }
      }
    }
  }
}