/* 7.21 tz-twitter-slider */
.tzTwitter-slider-box {
  position: relative;
  button {
    background: none;
    box-shadow: none;
    border: none;
    &.tz_meetup_twitter_prev {
      left: 35%;
    }
    &.tz_meetup_twitter_next {
      right: 35%;
  }
    i {
      color: #999999;
      font-size: 20px;
    }
  }
}
.tzTwitter-slider {
  &.owl-theme {
    .owl-controls {
      margin-top: 58px;
      .owl-page {
        span {
          width: 20px;
          height: 20px;
          border: 2px solid @color-theme;
          background: transparent;
          opacity: 1;
          transition: all 0.2s linear 0s;
          -moz-transition: all 0.2s linear 0s;
          -webkit-transition: all 0.2s linear 0s;
          -ms-transition: all 0.2s linear 0s;
          -o-transition: all 0.2s linear 0s;
          margin: 0 5px;
          &:hover {
            background: @color-theme;
          }
        }
        &.active {
          span {
            background: @color-theme;
          }
        }
      }
    }
  }
}
.tzTwitter-item {
  text-align: center;
  .tzTwitter-icon {
    background-image: url("images/border-tw.png");
    background-position: center;
    background-repeat: no-repeat;
    padding: 28px 0 32px;
    i.fa {
      font-size: 40px;
      color: #ffffff;
    }
  }
  span.tweet_text {
    font-weight: 300;
    font-size: 30px;
    color: #eeeeee;
    display: block;
    line-height: 1.6em;
    margin: 35px 0 40px 0;
    a {
      color: @color-theme;
    }
  }
  span.tz_tweet_meetup_name {
    margin-bottom: 5px;
    color: #aaaaaa;
    display: block;
  }
  span.tweet_time {
    display: block;
    font-size: 15px;
    color: #eeeeee;
  }
}