/* 7.20 tz-partner */
.tz-partner {
  position: relative;
    button.tz_partner_prevs {
      background: transparent;
      width: 40px;
      height: 40px;
      text-align: center;
      position: absolute;
      top: 50%;
      left: -65px;
      margin-top: -34px;
      border: 1px solid #fff;
      border-radius: 50%;
      -moz-border-radius: 50%;
      -webkit-border-radius: 50%;
      -ms-border-radius: 50%;
      -o-border-radius: 50%;
      transition: all 0.3s ease 0s;
      -webkit-transition: all 0.3s ease 0s;
      -moz-transition: all 0.3s ease 0s;
      -ms-transition: all 0.3s ease 0s;
      -o-transition: all 0.3s ease 0s;
      i.fa {
        color: #fff;
        font-size: 21px;
        padding: 9px 0;
      }
    }
    button.tz_partner_prevs:hover {
      background: #c93b4b;
      border: none;
    }
    button.tz_partner_nexts {
      background: transparent;
      width: 40px;
      height: 40px;
      text-align: center;
      position: absolute;
      top: 50%;
      right: -65px;
      margin-top: -34px;
      border: 1px solid #fff;
      border-radius: 50%;
      -moz-border-radius: 50%;
      -webkit-border-radius: 50%;
      -ms-border-radius: 50%;
      -o-border-radius: 50%;
      transition: all 0.3s ease 0s;
      -webkit-transition: all 0.3s ease 0s;
      -moz-transition: all 0.3s ease 0s;
      -ms-transition: all 0.3s ease 0s;
      -o-transition: all 0.3s ease 0s;
      i.fa {
        color: #fff;
        font-size: 21px;
        padding: 9px 0;
      }
    }
    button.tz_partner_nexts:hover {
      background: @color-theme;
      border: none;
    }
    .tz_partner_prevs_type2 {
      background: transparent;
      border: none;
      position: absolute;
      top: 50%;
      left: -75px;
      font-size: 60px;
      margin-top: -30px;
      color: #aaaaaa;
      cursor: pointer;
      transition: all 0.3s ease 0s;
      -webkit-transition: all 0.3s ease 0s;
      -moz-transition: all 0.3s ease 0s;
      -ms-transition: all 0.3s ease 0s;
      -o-transition: all 0.3s ease 0s;
      &:hover {
        color: @color-theme;
      }
    }
    .tz_partner_nexts_type2 {
      background: transparent;
      border: none;
      position: absolute;
      top: 50%;
      right: -75px;
      font-size: 60px;
      margin-top: -30px;
      color: #aaaaaa;
      cursor: pointer;
      transition: all 0.3s ease 0s;
      -webkit-transition: all 0.3s ease 0s;
      -moz-transition: all 0.3s ease 0s;
      -ms-transition: all 0.3s ease 0s;
      -o-transition: all 0.3s ease 0s;
      &:hover {
        color: @color-theme;
      }
    }
}
.partner-slider {
  cursor: w-resize;
  list-style: none;
  margin: 0;
  padding: 0;
  li {
    text-align: center;
  }
}