/* 10.22 contact_form7 */
.wpcf7-form {
  .tz_meetup_wpcf7-form {
    p {
      display: block;
      position: relative;
      margin-bottom: 20px;
      i.fa {
        position: absolute;
        top: 50%;
        right: 10px;
        color: #ccd1db;
        font-size: 20px;
        margin-top: -10px;
      }
      span {
        display: block;
        input {
          width: 100%;
          padding: 15px 10px;
          color: #95979d;
          font-family: @font-family-p;
          font-weight: 600;
          border: none;
          background: #f8f8f8;
          .border-radius(3px);
          &:focus {
            border: 1px solid @color-theme;
            background: transparent;
          }
        }
        textarea.wpcf7-textarea {
          width: 100%;
          padding: 15px 10px;
          color: #95979d;
          font-family: @font-family-p;
          font-weight: 600;
          border: none;
          background: #f8f8f8;
          .border-radius(3px);
          height: 185px;
          &:focus {
            border: 1px solid @color-theme;
            outline: -webkit-focus-ring-color auto 0;
            background: transparent;
          }
        }
      }
      input.wpcf7-submit {
        text-transform: uppercase;
        background: #e66423;
        border: none;
        .border-radius(3px);
        font-family: @font-family-p;
        font-weight: 700;
        color: #ffffff;
        font-size: 12px;
        width: 100%;
        padding: 16px 10px;
        letter-spacing: 2px;
      }
      &.tz_meetup_contact_textarea {
        i.fa {
          top: 20px;
        }
      }
    }
  }
  .tz_meetup_paypal_wpcf7-form {
    width: 60%;
    margin: 48px 0 0;
    p {
      margin-bottom: 15px;
      label {
        font-weight: 700;
        font-size: 13px;
        color: #7e8394;
        text-transform: uppercase;
        margin-bottom: 6px;
      }
      span {
        input {
          width: 100%;
          padding: 13px 20px;
          background: #ffffff;
          border: 2px solid #ffffff;
          .border-radius ( 5px );
          color: #53586b;
          font-size: 15px;
          line-height: 1.4em;
          transition: all 0.3s linear 0s;
          -moz-transition: all 0.3s linear 0s;
          -webkit-transition: all 0.3s linear 0s;
          -ms-transition: all 0.3s linear 0s;
          -o-transition: all 0.3s linear 0s;
          &:focus {
            border: 2px solid @color-theme;
          }
        }
      }
      &.tz_meetup_btn_paypal {
        margin: 45px 0 0;
        input {
          &.wpcf7-paypalsubmit {
            background: none;
            .border-radius ( 25px );
            border: 2px solid #ffffff;
            color: #ffffff;
            display: inline-block;
            font-size: 12px;
            font-weight: 700;
            letter-spacing: 3px;
            text-transform: uppercase;
            padding: 13px 35px;
          }
        }
      }
      &.tz_meetup_amout_item_paypal {
        visibility: hidden;
        height: 0;
        margin: 0;
          span {
            input {
              padding: 0;
            }
          }
      }
      &.tz_meetup_quantity_paypal {
        sub.tz_meetup_quantity_paypal_input {
          position: relative;
          display: block;
          &:before {
            background-color: #fff;
            content: "";
            display: block;
            height: 80%;
            pointer-events: none;
            position: absolute;
            right: 5px;
            top: 2px;
            width: 40px;
            z-index: 1;
            .border-radius ( 5px );
          }
          span {
            display: block;
            &:before {
              position: absolute;
              content: "\f0d7";
              font-family: 'FontAwesome';
              bottom: 18px;
              right: 25px;
              pointer-events: none;
              font-size: 18px;
              z-index: 2;
            }
            &:after {
              position: absolute;
              content: "\f0d8";
              font-family: 'FontAwesome';
              top: 18px;
              right: 25px;
              pointer-events: none;
              font-size: 18px;
              z-index: 2;
            }
          }
        }
      }
    }
    &.tz_meetup_paypal_2_wpcf7-form {
      width: 50%;
      p {
        span {
          input {
            background: #f8f8f8;
          }
        }
        &.tz_meetup_quantity_paypal sub.tz_meetup_quantity_paypal_input:before,
        &.tz_meetup_btn_paypal input.wpcf7-paypalsubmit,
        &.tz_meetup_btn_paypal input.wpcf7-paypalsubmit {
          background: #f8f8f8;
          color: #53586b;
        }
        &.tz_meetup_amout_item_paypal {
          height: 0;
        }
      }
    }
  }
}

@supports (overflow:-webkit-marquee) and (justify-content:inherit) {

  .wpcf7-form {
    .tz_meetup_paypal_wpcf7-form {
      p.tz_meetup_quantity_paypal {
        sub.tz_meetup_quantity_paypal_input:before {
          right: 2px;
        }
        sub.tz_meetup_quantity_paypal_input {
          span {
            &:before,
            &:after {
              right: 10px;
            }
          }
        }
      }
    }
  }

}